
import ViewBox from 'vux/src/components/view-box/index.vue'

export default{
name:"Information",
components:{
ViewBox
},
data(){

return {
vuegConfig:{
forwardAnim:'slideInRight',  //options所有配置可以写在这个对象里，会覆盖全局的配置
disable:false,//对于嵌套路由，默认为关闭动画，需要在组件的data.vuegConfig中配置disable为false启用
backAnim:'slideInLeft'
},
id:this.$store.state.listId,
content:""
}
},
mounted(){
document.documentElement.scrollTop=0
var _this=this
this.$http.get("https://www.xueguoguo.cn/wxapi/Article?funcid=info&id="+_this.id,{}).then(function(result){

_this.content=result.data.result[0].content
})

},methods:{

}
}



