
import ViewBox from 'vux/src/components/view-box/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

const list = () => ['专业概述','院校排名']
export default{
directives: {
TransferDom
},
name:"ProDetail",
components:{

Tab,
TabItem,
Swiper,
SwiperItem,
Loading,
ViewBox
},
data(){
return {
vuegConfig:{
forwardAnim:'slideInLeft',  //options所有配置可以写在这个对象里，会覆盖全局的配置
disable:false,//对于嵌套路由，默认为关闭动画，需要在组件的data.vuegConfig中配置disable为false启用
backAnim:'slideInRight'
},

id:this.$store.state.categoryId,
hostSchool:[],
SchoolArr:[],
brief:"",
destination:"",
maincourse:"",
index:0,
list2: list(),
demo2:'专业概述',
showLoad:false,
allScholl:[]
}
},
mounted(){
var _this=this;



this.$http.get("https://www.xueguoguo.cn/api/v1/major/detail?id="+this.$store.state.categoryId,{}).then(function(result){


_this.hostSchool=result.data.data.hotschool.split(',')
_this.maincourse=result.data.data.maincourse
_this.destination=result.data.data.destination
_this.brief=result.data.data.brief
_this.brief=_this.getbr(_this.brief)
_this.maincourse=_this.getbr(_this.maincourse)
_this.destination=_this.getbr(_this.destination)
_this.$nextTick(function() {
var height=$("#fullBox").height()*2/100+"rem"

_this.$refs.swiper.xheight = height
})

_this.getSchoolData(_this.hostSchool)

})


},
methods:{

goSchool(id){
this.$store.state.id =id
this.$router.push({
path: '/School'
})
},
change(index){
var _this=this
if(index==0){
this.$nextTick(function() {
var height=$("#fullBox").height()*2/100+"rem"

this.$refs.swiper.xheight = height
})
}else{
$(".weui-toast").css("min-height","2.5rem")
this.showLoad=true
this.SchoolArr=this.allScholl
setTimeout(function(){
_this.sort(_this.SchoolArr)

_this.$nextTick(function() {
var height=document.documentElement.clientHeight*2/100-1.8+"rem"

_this.$refs.swiper.xheight = height
})
_this.showLoad=false
},500)




}

},
getbr(str){

if(str.indexOf("\n")!=-1){
var arr=[]
var newstr=""
arr=str.split("\n");

for(var i=0;i<arr.length;i++){
if(i>0){
newstr+="</br>"+arr[i]
}else{
newstr+=arr[i]
}

}
return newstr
}else{
return str
}

},
getSchoolData(arr){
var _this=this
for(var i=0;i<arr.length;i++){
this.$http.get("https://www.xueguoguo.cn/api/v1/colleges/query?names="+arr[i],{})
.then(function(result){

if(result.data.msg=="没有数据"){

}else{
_this.allScholl.push(result.data.data[0])
}

})
}
this.sort(this.allScholl)
}
,
sort(arr){
var newarr=[]
for(var i=0;i<arr.length-1;i++){
for(var j=0;j<arr.length-1-i;j++){

if(arr[j].rank=="0"){
var item=arr[j]
arr.splice(j,1)
newarr.push(item)

}else{
if(Number(arr[j].rank)>Number(arr[j+1].rank)){
var temp=arr[j];
arr[j]=arr[j+1];
arr[j+1]=temp;
}
}

}
}
for(var k=0;k<newarr.length;k++){
arr.push(newarr[k])
}
return arr
}
}
}



