
import Search from 'vux/src/components/search/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'

export default{
name:'SearchPro',
components:{
Search,
ViewBox
},
data(){
return {
vuegConfig:{
forwardAnim:'slideInRight',  //options所有配置可以写在这个对象里，会覆盖全局的配置
disable:false,//对于嵌套路由，默认为关闭动画，需要在组件的data.vuegConfig中配置disable为false启用
backAnim:'slideInLeft'
},
results: [],
value: this.$store.state.ProSearch,
historyArr:["会计","管理学","法学"]
}
},
mounted(){

$('.vux-search-box').css({"width":"5.38rem","left":"1rem"});


},methods:{
back() {

this.$router.go(-1);

},
getResult(val) {


},
onFocus() {

},
onCancel() {

},
onBlur() {

this.$store.state.ProSearch=this.value

}
}
}



