
import ViewBox from 'vux/src/components/view-box/index.vue'

export default{
name:"TestResult",
components:{
ViewBox
},
data(){
return {
vuegConfig:{
forwardAnim:'slideInRight',
disable:false,
backAnim:'slideInLeft'
},
content:"",
jobs:""
}
},
mounted(){
var _this=this;
this.$http.get("https://www.xueguoguo.cn/api/v1/ptest/paper?id="+this.$store.state.TestId,{})
.then(function(result){

for(var i=0;i<result.data.data.answers.length;i++){
if(result.data.data.answers[i].id==_this.$store.state.TestResult){
_this.content=result.data.data.answers[i].brief
_this.jobs=result.data.data.answers[i].jobs
}
}
})


},
methods:{

goJob(){
this.$router.push({
path:"/SearchJob"
})
}
}
}



