
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Picker from 'vux/src/components/picker/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
directives: {
TransferDom
},
name: "BatchLine",
components: {

Tab,
TabItem,
Picker,
Loading,
Search,
Toast,
ViewBox,
XHeader
},
data() {
return {
vuegConfig: {
forwardAnim: 'bounceInUp', //options所有配置可以写在这个对象里，会覆盖全局的配置
disable: false, //对于嵌套路由，默认为关闭动画，需要在组件的data.vuegConfig中配置disable为false启用
backAnim: 'bounceInDown'
},
schoolData: [
[

]
],
proData: [
[]
],
sub: [
["文科", "理科"]
],
slots: [this.$store.state.position],
maskStyle: {
height: ""
},
transfer: {

subject: [""],
fromPlace: [""],
school: [""],
pro: [""]
},
fromPlace: this.$store.state.fromPlace,
subject: this.$store.state.subject,
LineTitle: ["年份", "本科一批", "本科二批", "本科三批", "专科分"],
SchoolTitle: ["年份", "最高分", "最低分", "平均分","录取分数", "录取人数","投档分数","投档人数"],
Five: [],
Six: [],
Seven: [],
showLoad: false,
LineBox: false,
SchoolBox: false,
ProBox: false,
activeIndex: 0,
Searchvalue: "",
Provalue: "",
SearchFullData: [],
SearchProData: [],
school: "",
pro: "",
college_id: "",
pro_id: "",
input: false,
inputPro: false,
inputMark: false,
noData: false,
noPosition: false,
noSubject: false,
noSchool: false,
noPro: false,
noMark:false,
MarkError:false,
mark: "",
year13:false,
year14:false,
year15:false,
year16:false,
rank13:"",
rank14:"",
rank15:"",
rank16:"",
SchoolArr:[],
colledge_ids:[]


}
},

mounted() {
$(".weui-toast").css("min-height", "1rem")
this.maskStyle.height = document.documentElement.clientHeight * 2 / 100 + "rem";
if(this.$store.state.fromPlace == "") {

this.transfer.fromPlace[0] = this.slots[0][0]
} else {
this.transfer.fromPlace[0] = this.$store.state.fromPlace
}
if(this.$store.state.subject == "") {
this.transfer.subject[0] = this.sub[0][0]
} else {
this.transfer.subject[0] = this.$store.state.subject
}

},
methods: {

comfirm() {
$(".weui-toast").css("min-height", "1rem")

if(this.fromPlace == "") {
this.noPosition = true;
return false;
} else if(this.subject == "") {
this.noSubject = true;
return false;
} else {
var _this = this;
_this.Five.length = 0;
_this.Six.length = 0
_this.Seven.length = 0;
_this.rank13=""
_this.rank14=""
_this.rank15=""
_this.rank16=""
if(this.activeIndex == 0) {
this.showLoad = true;

this.$http.get("https://www.xueguoguo.cn/api/v1/college/batchLine?examType=" + this.subject + "&province=" + this.fromPlace, {

})
.then(function(result) {
if(result.data.msg == "没有数据") {

_this.showLoad = false
_this.noData = true
} else {
setTimeout(function() {
_this.LineBox = true

for(var i = 0; i < result.data.data.length; i++) {
if(result.data.data[i].year == "2015") {

_this.Five.push(result.data.data[i].batch1)
_this.Five.push(result.data.data[i].batch2)
_this.Five.push(result.data.data[i].batch3)
_this.Five.push(result.data.data[i].zhuanke)
}
if(result.data.data[i].year == "2016") {
_this.Six.push(result.data.data[i].batch1)
_this.Six.push(result.data.data[i].batch2)
_this.Six.push(result.data.data[i].batch3)
_this.Six.push(result.data.data[i].zhuanke)
}
if(result.data.data[i].year == "2017") {
_this.Seven.push(result.data.data[i].batch1)
_this.Seven.push(result.data.data[i].batch2)
_this.Seven.push(result.data.data[i].batch3)
_this.Seven.push(result.data.data[i].zhuanke)
}
}
_this.replace(_this.Five);
_this.replace(_this.Six);
_this.replace(_this.Seven);
_this.showLoad = false
}, 500)
}

})
} else if(this.activeIndex == 1) {
if(this.college_id == "") {
this.noSchool = true;
return false;
} else {
this.showLoad = true;
this.$http.get("https://www.xueguoguo.cn/api/v1/college/collegeLine?examType=" + this.subject + "&province=" + this.fromPlace + "&college_id=" + this.college_id, {}).then(function(result) {

if(result.data.msg == "没有数据") {

_this.showLoad = false
_this.noData = true
} else {

setTimeout(function() {
_this.SchoolBox = true
for(var i = 0; i < result.data.data.length; i++) {
if(result.data.data[i].year == "2015") {

_this.Five.push(result.data.data[i].highest)
_this.Five.push(result.data.data[i].lowest)
_this.Five.push(result.data.data[i].average)

_this.Five.push(result.data.data[i].luqu)
_this.Five.push(result.data.data[i].luqunum)
_this.Five.push(result.data.data[i].toudang)
_this.Five.push(result.data.data[i].toudangnum)
}
if(result.data.data[i].year == "2016") {
_this.Six.push(result.data.data[i].highest)
_this.Six.push(result.data.data[i].lowest)
_this.Six.push(result.data.data[i].average)
_this.Six.push(result.data.data[i].luqu)
_this.Six.push(result.data.data[i].luqunum)
_this.Six.push(result.data.data[i].toudang)
_this.Six.push(result.data.data[i].toudangnum)
}
if(result.data.data[i].year == "2017") {
_this.Seven.push(result.data.data[i].highest)
_this.Seven.push(result.data.data[i].lowest)
_this.Seven.push(result.data.data[i].average)
_this.Seven.push(result.data.data[i].luqu)
_this.Seven.push(result.data.data[i].luqunum)
_this.Seven.push(result.data.data[i].toudang)
_this.Seven.push(result.data.data[i].toudangnum)
}
}
_this.replace(_this.Five);
_this.replace(_this.Six);
_this.replace(_this.Seven);
_this.showLoad = false
}, 500)
}

})
}

} else if(this.activeIndex == 2) {

if(this.college_id == "") {
this.noSchool = true;
return false;
} else if(this.pro_id == "") {
this.noPro = true;
return false;
} else {
this.showLoad = true;
this.$http.get("https://www.xueguoguo.cn/api/v1/college/majorLine?examType=" + this.subject + "&province=" + this.fromPlace + "&college_id=" + this.college_id + "&major_id=" + this.pro_id, {}).then(function(result) {


if(result.data.msg == "没有数据") {

_this.showLoad = false
_this.noData = true
} else {
setTimeout(function() {
_this.ProBox = true
for(var i = 0; i < result.data.data.length; i++) {
if(result.data.data[i].year == "2015") {

_this.Five.push(result.data.data[i].highest)
_this.Five.push(result.data.data[i].lowest)
_this.Five.push(result.data.data[i].average)
_this.Five.push(result.data.data[i].luqu)
}
if(result.data.data[i].year == "2016") {
_this.Six.push(result.data.data[i].highest)
_this.Six.push(result.data.data[i].lowest)
_this.Six.push(result.data.data[i].average)
_this.Six.push(result.data.data[i].luqu)
}
if(result.data.data[i].year == "2017") {
_this.Seven.push(result.data.data[i].highest)
_this.Seven.push(result.data.data[i].lowest)
_this.Seven.push(result.data.data[i].average)
_this.Seven.push(result.data.data[i].luqu)
}
}
_this.replace(_this.Five);
_this.replace(_this.Six);
_this.replace(_this.Seven);
_this.showLoad = false
}, 500)

}

})
}

}else{
if(this.mark==""){
this.noMark=true;
return false;
}else if(this.mark>750||this.mark<0){
this.MarkError=true;
return false;
}

else{
this.showLoad = true;
this.$http.get("https://www.xueguoguo.cn/api/v1/college/provinceRank?examType=" + this.subject + "&province=" + this.fromPlace + "&score=" + this.mark , {}).then(function(result) {


if(result.data.msg == "没有数据") {

_this.showLoad = false
_this.noData = true
} else {
setTimeout(function() {
_this.MarkBox = true
if(result.data.data.rank13!=undefined){
_this.year13=true
_this.rank13=result.data.data.rank13

}else{
_this.year13=false
}
if(result.data.data.rank14!=undefined){
_this.year14=true
_this.rank14=result.data.data.rank14
}
else{
_this.year14=false
}

if(result.data.data.rank15!=undefined){
_this.year15=true
_this.rank15=result.data.data.rank15
}
else{
_this.year15=false
}
if(result.data.data.rank16!=undefined){
_this.year16=true
_this.rank16=result.data.data.rank16
}
else{
_this.year16=false
}
_this.colledge_ids=result.data.data.colledge_ids.split(",")

for(var i=0;i<_this.colledge_ids.length;i++){
_this.$http.get("https://www.xueguoguo.cn/api/v1/colleges/detail/?id="+_this.colledge_ids[i],{})
.then(function(response){
_this.SchoolArr.push(response.data.data)
})
}


_this.showLoad = false
}, 500)

}

})



}




}

}

},
onItemClick(index) {
this.activeIndex = index;
if(this.activeIndex == 1 || this.activeIndex == 2) {
this.input = true
this.inputMark = false
if(this.activeIndex == 2) {
this.inputPro = true
} else {
this.inputPro = false
}

} else if(this.activeIndex == 3) {
this.inputMark = true;
this.inputPro = false
this.input = false
} else {
this.inputMark = false
this.inputPro = false
this.input = false
}
this.LineBox = false
this.SchoolBox = false
this.ProBox=false
this.MarkBox=false
},
goSchool(id){
this.$store.state.id =id
this.$router.push({
path: '/School'
})


},
placeChange(value) {

this.transfer.fromPlace[0] = value[0];
},
subChange(value) {

this.transfer.subject[0] = value[0];
},
closePlace() {
$('#place').fadeOut()
},
comfirmPlace() {
this.fromPlace = this.transfer.fromPlace[0]

$('#place').fadeOut()
},
closeSub() {
$('#subject').fadeOut()
},
comfirmSub() {
this.subject = this.transfer.subject[0]

$('#subject').fadeOut()
},
placeShow() {
$("#place").fadeIn()
},
subShow() {
$("#subject").fadeIn()
},
replace(arr) {
for(var i = 0; i < arr.length; i++) {
if(arr[i] == "0") {
arr[i] = "-"
}
}
},
onFocus() {
$('.vux-search-box').css({
"width": "5.38rem",
"left": "0.24rem"
});
this.$refs.input.blur()

$("#school").fadeIn()
this.$refs.search.setFocus()
},
FocusPro() {
$('.vux-search-box').css({
"width": "5.38rem",
"left": "0.24rem"
});
this.$refs.pro.blur()
$("#pro").fadeIn()
this.$refs.searchPro.setFocus()
},
schoolChange(value) {
this.transfer.school[0] = value[0]
},
proChange(value) {
this.transfer.pro[0] = value[0]
},
getResult(val) {
this.Searchvalue = val

},
getPro(val) {
this.Provalue = val
},
searchSchool() {
var _this = this;
this.schoolData[0].length = 0;
this.SearchFullData.length = 0;
this.$http.get("https://www.xueguoguo.cn/api/v1/college/autoCompletionList?keyword=" + this.Searchvalue, {}).then(function(result) {


if(result.data.msg == "没有数据") {
_this.noData = true
} else {
_this.SearchFullData = result.data.data
for(var i = 0; i < result.data.data.length; i++) {
_this.schoolData[0].push(result.data.data[i].name)
}
}

})
},
Prosearch() {
var _this = this;
this.proData[0].length = 0;
this.SearchProData.length = 0;
this.$http.get("https://www.xueguoguo.cn/api/v1/major/autoCompletionList?keyword=" + this.Provalue, {}).then(function(result) {

if(result.data.msg == "没有数据") {
_this.noData = true
} else {
_this.SearchProData = result.data.data

for(var i = 0; i < result.data.data.length; i++) {
_this.proData[0].push(result.data.data[i].name)
}
}

})

},
comfirmSchool() {
this.school = this.transfer.school[0]
for(var i = 0; i < this.SearchFullData.length; i++) {
if(this.school == this.SearchFullData[i].name) {
this.college_id = this.SearchFullData[i].id
}
}

$("#school").fadeOut()
},
comfirmPro() {
this.pro = this.transfer.pro[0]
for(var i = 0; i < this.SearchProData.length; i++) {
if(this.pro == this.SearchProData[i].name) {
this.pro_id = this.SearchProData[i].id
}
}
$("#pro").fadeOut()
}
}
}



