
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
name: "Professional",
components: {
XHeader,
Toast,
Confirm,
Tabbar,
TabbarItem,
ViewBox
},
data: function() {
return {
vuegConfig:{
forwardAnim:'bounceInUp',  //options所有配置可以写在这个对象里，会覆盖全局的配置
disable:false,//对于嵌套路由，默认为关闭动画，需要在组件的data.vuegConfig中配置disable为false启用
backAnim:'bounceInDown'
},
Proarr: this.$store.state.course_category,
pcount: 0,
Pro: false,
leave: false,
comfirmBtn: false,
comfirmFlag: false,
arr:[]
}
},
mounted() {
$(".weui-tabbar").css("position","fixed")
$('.weui-dialog').css("height", "3rem")
if(this.$store.state.pro!=""){
this.arr=this.$store.state.pro.split(",")

this.pcount=this.arr.length
}
this.flagArr = this.flag()
this.background();
this.getHeight()

},

methods: {
back() {
if(this.comfirmFlag) {
this.$router.go(-1);

} else {
this.leave = true
}

},
onLeave() {
this.$store.state.pro=this.arr.join(",")
this.$router.go(-1);

},
select(str,index) {

if(this.flagArr[index]) {
$('.checkbox').eq(index).css({
"background": "#2B94FA",
"color": "#FFFFFF"
})
this.flagArr[index] = false;
var newobj = str;
if(this.$store.state.pro!=""){
this.arr.push(newobj);

}else{
this.$store.state.pro+=str
this.arr=this.$store.state.pro.split(",")
}

this.pcount = this.arr.length;

} else {
$('.checkbox').eq(index).css({
"background": "#FFFFFF",
"color": "#2B94FA"
})
this.flagArr[index] = true
for(var i = 0; i < this.arr.length; i++) {
if(this.arr[i] == str) {

this.arr.splice(i, 1)
}
}

this.pcount =this.arr.length
}
},
flag() {

var len = this.Proarr.length;
var flagArr = [];
for(var i = 0; i < len; i++) {
flagArr[i] = true;
}
return flagArr;
},
cancel() {
this.Pro = true

},
background() {

for(var j=0;j<this.Proarr.length;j++){
for(var i = 0; i < this.arr.length; i++) {
if(this.arr[i]==this.Proarr[j]){

$('.checkbox').eq(j).css({
"background": "#2B94FA",
"color": "#FFFFFF"
});
this.flagArr[j] = false;
}


}


}
},
comfirm() {

this.$store.state.pro=this.arr.join(",")
this.comfirmBtn = true
},
onComfirm() {
this.comfirmBtn = false;
this.comfirmFlag = true;
this.back()
},
getHeight() {

var Height = document.documentElement.clientHeight * 2 / 100 + "rem"
$('.big_box').css("min-height", Height);

},
onDelete() {
$('.checkbox').each(function(i) {
$('.checkbox').eq(i).css({
"background": "#FFFFFF",
"color": "#2B94FA"
});
});
this.$store.state.pro= "";
this.arr=[]
this.pcount = 0;
for(var i = 0; i < this.flagArr.length; i++) {
this.flagArr[i] = true
}

}
}
}



