
import Zepto from '../../static/zepto.min.js'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

let galleryThumbs
export default {
directives: {
TransferDom
},
name: 'Home',
components: {
ViewBox,
Confirm
},
data() {
return {
vuegConfig: {
forwardAnim: 'slideInRight', //options所有配置可以写在这个对象里，会覆盖全局的配置
disable: false, //对于嵌套路由，默认为关闭动画，需要在组件的data.vuegConfig中配置disable为false启用
backAnim: 'slideInLeft'
},

mark: this.$store.state.mark,
subject: this.$store.state.subject,
fromPlace: this.$store.state.fromPlace,
goTop: false,
list: this.$store.state.newsArr,
showV:false
}
},
mounted() {
var _this = this;

if(this.$store.state.token!=""){
this.$http.get("https://www.xueguoguo.cn/api/v1/aspiration/detail?token="+this.$store.state.token, {}).then(function(response) {

_this.mark = response.data.data.score;
_this.subject = response.data.data.examType
_this.fromPlace = response.data.data.province

});
}



if(this.$store.state.newsArr.length == 0) {

this.$http.get("https://www.xueguoguo.cn/wxapi/Article?funcid=list&id=4", {}).then(function(result) {
_this.$store.state.newsArr = result.data.list
_this.list = _this.$store.state.newsArr
});
} else {
this.list = this.$store.state.newsArr
}

for(var i = 0; i < this.list.length; i++) {
var index = this.list[i].createtime.indexOf(" ")
this.list[i].createtime.substr()
}
},
methods: {
goSchoolSelect() {
this.$router.push({
path: '/SchoolSelect'
})
},

goServe(val) {
this.$store.state.ServeType = val
this.$router.push({
path: '/Serve'
})
},
goMark() {
$('.weui-dialog').css("height", "3.2rem")
if(this.$store.state.token==""){
this.$router.push({
path: '/Login'
})
this.$store.state.Inflag = true
}else if(this.$store.state.isVip==false){
this.showV=true
}
else{
this.$router.push({
path: '/Mark'
})
}


},
goSelectPro() {
this.$router.push({
path: '/SelectPro'
})
},
goSame() {
this.$router.push({
path: '/SameMark'
})
},
goLine() {
this.$router.push({
path: '/BatchLine'
})
},
goTest() {
this.$router.push({
path: '/Test'
})
},
goVol() {
$('.weui-dialog').css("height", "3.2rem")
if(this.$store.state.token==""){
this.$router.push({
path: '/Login'
})
this.$store.state.Inflag = true
}else if(this.$store.state.isVip==false){
this.showV=true
}
else{
this.$router.push({
path: '/Volunteer'
})
}



},
goEach(id) {
this.$store.state.listId = id

this.$router.push({
path: '/Information'
})
},
Go(){
window.location.href="https://www.xueguoguo.cn/jump.html"
}
}
}



