
import Scroller from 'vux/src/components/scroller/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'

export default{
name:"SearchJob",
components:{

Scroller,
ViewBox
},
data(){
return{
vuegConfig:{
forwardAnim:'slideInRight',
disable:false,
backAnim:'slideInLeft'
},
JobArr:this.$store.state.Job_first,
moreArr:this.$store.state.Job_Second[0]
}
},

mounted(){
$("#course .course").eq(0).css({"background":"#2B94FA","color":"#ffffff"});
this.$nextTick(function() {
this.$refs.scroll.reset()
})
},

methods:{

changeJob(index){

this.moreArr=this.$store.state.Job_Second[index]
$("#course .course").eq(index).css({"background":"#2B94FA","color":"#ffffff"}).siblings(".course").css({"background":"#FFFFFF","color":"#1B1E1E"});

},
goJobType(str){
this.$store.state.JobTitle=str;
this.$router.push({
path: '/JobType'
})
}
}
}



