
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default{
directives: {
TransferDom
},
name:"SameMark",
components:{

Picker,
Loading,
Toast,
XHeader,
ViewBox
},
data(){
return {
vuegConfig:{
forwardAnim: 'bounceInUp', //options所有配置可以写在这个对象里，会覆盖全局的配置
disable: false, //对于嵌套路由，默认为关闭动画，需要在组件的data.vuegConfig中配置disable为false启用
backAnim: 'bounceInDown'
},
SchoolArr:[],
slots:[this.$store.state.position],
sub:[["文科","理科"]],
value:this.$store.state.mark,
fromPlace:this.$store.state.fromPlace,
subject:this.$store.state.subject,
transfer:{

subject:[""],
fromPlace:[""]
},

maskStyle:{
height:""
},
showLoad:false,
showErrorranking:false,
noData:false,
showErrorPlace:false,
showErrorSubject:false
}
},
mounted(){


this.maskStyle.height=document.documentElement.clientHeight*2/100+"rem";

if(this.$store.state.fromPlace==""){

this.transfer.fromPlace[0]=this.slots[0][0]
}else{
this.transfer.fromPlace[0]=this.$store.state.fromPlace
}
if(this.$store.state.subject==""){
this.transfer.subject[0]=this.sub[0][0]
}else{
this.transfer.subject[0]=this.$store.state.subject
}
},
methods:{

goSchool(id){
this.$store.state.id =id
this.$router.push({
path: '/School'
})
},
placeShow(){
$("#place").fadeIn()
},
subShow(){
$("#subject").fadeIn()
},
placeChange(value){

this.transfer.fromPlace[0]=value[0];
},
subChange(value){

this.transfer.subject[0]=value[0];
},
closePlace(){
$('#place').fadeOut()
},
comfirmPlace(){
this.fromPlace=this.transfer.fromPlace[0]

$('#place').fadeOut()
}
,
closeSub(){
$('#subject').fadeOut()
},
comfirmSub(){
this.subject=this.transfer.subject[0]

$('#subject').fadeOut()
},
getResult(){
$(".weui-toast").css("min-height","1rem")

if(this.value>750||this.value<0){
this.showErrorranking=true
}
else if(this.fromPlace==""){
this.showErrorPlace=true
}else if(this.subject==""){
this.showErrorSubject=true
}
else{
this.showLoad=true;
var _this=this;
this.SchoolArr.length=0

this.$http.get("https://www.xueguoguo.cn/api/v1/colleges/tfqx?province="+this.fromPlace+"&score="+this.value+"&examType="+this.subject,{}).then(function(result){


if(result.data.msg=="没有数据"){
setTimeout(function(){
_this.showLoad=false
_this.noData=true
},500)
}else{
setTimeout(function(){
_this.SchoolArr=result.data.data
_this.showLoad=false
_this.noData=false
},500)
}



})
}

}
}

}



