
import Progress from '../../static/progress.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default{
directives: {
TransferDom
},
name:"Volunteer",
components:{
Alert,
XHeader,
Group,
ViewBox,
Tabbar, TabbarItem,
Scroller,
Loading
},
data(){
return {
vuegConfig:{
forwardAnim: 'bounceInUp', //options所有配置可以写在这个对象里，会覆盖全局的配置
disable: false, //对于嵌套路由，默认为关闭动画，需要在组件的data.vuegConfig中配置disable为false启用
backAnim: 'bounceInDown'
},

mark:this.$store.state.mark,

fromPlace:this.$store.state.fromPlace,
subject:this.$store.state.subject,
show:false,
Ben:true,
Zhuan:true,
BenAdd:true ,
BenSign:"",
ZhuanSign:"",
ZhuanAdd:true,
BenArr:[],
ZhuanArr:[],
showLoad:false
}
},
mounted(){
$(".weui-toast").css("min-height","2.5rem")
console.log(this.$store.state.VolData)

if(this.$store.state.VolData.data==undefined){
this.showLoad=true;
var _this=this
this.$http.get("https://www.xueguoguo.cn/api/v1/simulate/detail?token="+this.$store.state.token,{}).then(function(result){
setTimeout(function(){
_this.$store.state.VolData=JSON.parse(JSON.stringify(result.data.data))
console.log(_this.$store.state.VolData)
_this.BenArr=result.data.data.data.batchs[0].colleges
_this.ZhuanArr=result.data.data.data.batchs[1].colleges
_this.setAddSign("本科批次")
_this.setAddSign("专科批次")



_this.writeSign(_this.BenArr)
_this.writeSign(_this.ZhuanArr)





var Benlen=_this.BenArr.length
var Zhuanlen=_this.ZhuanArr.length

if(Benlen>0){
if(Benlen!=15){
Benlen+=1
}
$(".first_li").children(".school_big_box").css({"height":Benlen*4+"rem"})
$(".first_li").children(".top").children("i").css({"transform":"rotate(90deg)"})
_this.Ben=false
}else{
$(".first_li").children(".school_big_box").css({"height":"0rem"})
$(".first_li").children(".top").children("i").css({"transform":"rotate(0deg)"})
_this.Ben=true
}
if(Zhuanlen>0){
if(Zhuanlen!=15){
Zhuanlen+=1
}
$(".second_li").children(".school_big_box").animate({"height":Zhuanlen*4+"rem"})
$(".second_li").children(".top").children("i").animate({"transform":"rotate(90deg)"})
_this.Zhuan=false
}else{
$(".second_li").children(".school_big_box").css({"height":"0rem"})
$(".second_li").children(".top").children("i").css({"transform":"rotate(0deg)"})
_this.Zhuan=true
}





_this.$nextTick(function() {

_this.draw();
})
_this.showLoad=false
},500)



})
}else{
this.BenArr=this.$store.state.VolData.data.batchs[0].colleges
this.ZhuanArr=this.$store.state.VolData.data.batchs[1].colleges
this.setAddSign("本科批次")
this.setAddSign("专科批次")
this.writeSign(this.BenArr)
this.writeSign(this.ZhuanArr)
var Benlen=this.BenArr.length
var Zhuanlen=this.ZhuanArr.length

if(Benlen>0){
if(Benlen!=15){
Benlen+=1
}
$(".first_li").children(".school_big_box").css({"height":Benlen*4+"rem"})
$(".first_li").children(".top").children("i").css({"transform":"rotate(90deg)"})
this.Ben=false
}else{
$(".first_li").children(".school_big_box").css({"height":"0rem"})
$(".first_li").children(".top").children("i").css({"transform":"rotate(0deg)"})
this.Ben=true
}
if(Zhuanlen>0){
if(Zhuanlen!=15){
Zhuanlen+=1
}
$(".second_li").children(".school_big_box").animate({"height":Zhuanlen*4+"rem"})
$(".second_li").children(".top").children("i").animate({"transform":"rotate(90deg)"})
this.Zhuan=false
}else{
$(".second_li").children(".school_big_box").css({"height":"0rem"})
$(".second_li").children(".top").children("i").css({"transform":"rotate(0deg)"})
this.Zhuan=true
}





this.$nextTick(function() {

this.draw();
})
}






$(".weui-dialog").css({"height":"3rem","width":"80%"})

if(this.mark==""||this.fromPlace==""||this.subject==""){
var _this=this
setTimeout(function(){
_this.show=true
},500)
}






},
methods:{
draw() {

$(".myChart").each(function() {
$(this).ringChart();
})
},
setAddSign(str){
if(str=="本科批次"){
if(this.BenArr.length==0){
this.BenSign="A"
this.BenAdd=true
}
else if(this.BenArr.length==1){
this.BenSign="B"
this.BenAdd=true
}else if(this.BenArr.length==2){
this.BenSign="C"
this.BenAdd=true
}else if(this.BenArr.length==3){
this.BenSign="D"
this.BenAdd=true
}else if(this.BenArr.length==4){
this.BenSign="E"
this.BenAdd=true
}else if(this.BenArr.length==5){
this.BenSign="F"
this.BenAdd=true
}else if(this.BenArr.length==6){
this.BenSign="G"
this.BenAdd=true
}else if(this.BenArr.length==7){
this.BenSign="H"
this.BenAdd=true
}else if(this.BenArr.length==8){
this.BenSign="I"
this.BenAdd=true
}else if(this.BenArr.length==9){
this.BenSign="J"
this.BenAdd=true
}else if(this.BenArr.length==10){
this.BenSign="K"
this.BenAdd=true
}else if(this.BenArr.length==11){
this.BenSign="L"
this.BenAdd=true
}else if(this.BenArr.length==12){
this.BenSign="M"
this.BenAdd=true
}else if(this.BenArr.length==13){
this.BenSign="N"
this.BenAdd=true
}else if(this.BenArr.length==14){
this.BenSign="O"
this.BenAdd=true
}else{
this.BenAdd=false
}



}else{

if(this.ZhuanArr.length==0){
this.ZhuanSign="A"
this.ZhuanAdd=true
}
else if(this.ZhuanArr.length==1){
this.ZhuanSign="B"
this.ZhuanAdd=true
}else if(this.ZhuanArr.length==2){
this.ZhuanSign="C"
this.ZhuanAdd=true
}else if(this.ZhuanArr.length==3){
this.ZhuanSign="D"
this.ZhuanAdd=true
}else if(this.ZhuanArr.length==4){
this.ZhuanSign="E"
this.ZhuanAdd=true
}else if(this.ZhuanArr.length==5){
this.ZhuanSign="F"
}else if(this.ZhuanArr.length==6){
this.ZhuanSign="G"
this.ZhuanAdd=true
}else if(this.ZhuanArr.length==7){
this.ZhuanSign="H"
this.ZhuanAdd=true
}else if(this.ZhuanArr.length==8){
this.ZhuanSign="I"
this.ZhuanAdd=true
}else if(this.ZhuanArr.length==9){
this.ZhuanSign="J"
this.ZhuanAdd=true
}else if(this.ZhuanArr.length==10){
this.ZhuanSign="K"
this.ZhuanAdd=true
}else if(this.ZhuanArr.length==11){
this.ZhuanSign="L"
this.ZhuanAdd=true
}else if(this.ZhuanArr.length==12){
this.ZhuanSign="M"
this.ZhuanAdd=true
}else if(this.ZhuanArr.length==13){
this.ZhuanSign="N"
this.ZhuanAdd=true
}else if(this.ZhuanArr.length==14){
this.ZhuanSign="O"
this.ZhuanAdd=true
}else{
this.ZhuanAdd=false
}





}
},
writeSign(arr){

for(var i=0;i<arr.length;i++){
if(i==0){
arr[i].sign="A"
}
else if(i==1){
arr[i].sign="B"
}else if(i==2){
arr[i].sign="C"
}else if(i==3){
arr[i].sign="D"
}else if(i==4){
arr[i].sign="E"
}else if(i==5){
arr[i].sign="F"
}else if(i==6){
arr[i].sign="G"
}else if(i==7){
arr[i].sign="H"
}else if(i==8){
arr[i].sign="I"
}else if(i==9){
arr[i].sign="J"
}else if(i==10){
arr[i].sign="K"
}else if(i==11){
arr[i].sign="L"
}else if(i==12){
arr[i].sign="M"
}else if(i==13){
arr[i].sign="N"
}else if(i==14){
arr[i].sign="O"
}
if(arr[i].majors!=""){
arr[i].show=false
}else{
arr[i].show=true
}

}



},



goSelect(str){

this.$store.state.VolType=str
this.$router.push({
path: '/VolSelect'
})



},
onHide () {
this.show=false
this.goMark()
},
showMoreBen(){

if(this.Ben){
var len=this.BenArr.length

if(len!=15){
len+=1
}
setTimeout(function(){
$(".first_li").children(".school_big_box").animate({"height":len*4+"rem"})
})


$(".first_li").children(".top").children("i").animate({"transform":"rotate(90deg)"})
this.Ben=false
}else{
$(".first_li").children(".school_big_box").animate({"height":"0rem"})
$(".first_li").children(".top").children("i").animate({"transform":"rotate(0deg)"})
this.Ben=true
}

},
showMoreZhuan(){

if(this.Zhuan){
var len=this.ZhuanArr.length
if(len!=15){
len+=1
}
setTimeout(function(){
$(".second_li").children(".school_big_box").animate({"height":len*4+"rem"})
})
$(".second_li").children(".top").children("i").animate({"transform":"rotate(90deg)"})
this.Zhuan=false
}else{
$(".second_li").children(".school_big_box").animate({"height":"0rem"})
$(".second_li").children(".top").children("i").animate({"transform":"rotate(0deg)"})
this.Zhuan=true
}



},
goSelectPro(id,str){
this.$store.state.VolId=id
this.$store.state.VolType=str
this.$router.push({
path: '/VolProSelect'
})


},


goMark(){
this.$store.state.Inflag=false
this.$router.push({
path: '/Mark'
})


},
delect(str,obj){
if(str=="本科批次"){
for(var i=0;i<this.BenArr.length;i++){
if(obj==this.BenArr[i]){
this.$store.state.VolData.data.batchs[0].colleges.splice(i,1)
this.BenArr.splice(i,1)
this.writeSign(this.BenArr)

}


}
var len=this.BenArr.length

if(len!=15){
len+=1
}


$(".first_li").children(".school_big_box").css({"height":len*4+"rem"})
}else{
for(var i=0;i<this.ZhuanArr.length;i++){
if(obj==this.ZhuanArr[i]){
this.$store.state.VolData.data.batchs[1].colleges.splice(i,1)
this.ZhuanArr.splice(i,1)
this.writeSign(this.ZhuanArr)

}
}
var len=this.ZhuanArr.length
if(len!=15){
len+=1
}


$(".second_li").children(".school_big_box").css({"height":len*4+"rem"})
}
this.setAddSign(str)
},
comfrim(){
var _this=this
$.ajax({
type: "post",
url: "https://www.xueguoguo.cn/api/v1/simulate/update?token="+this.$store.state.token,
async: false,
data: JSON.stringify(this.$store.state.VolData)

}).done(function(result) {


_this.$router.push({
path: '/Report'
})


});
}
}
}



