
import Zepto from '../../static/zepto.min.js'
import Progress from '../../static/progress.js'
import ViewBox from 'vux/src/components/view-box/index.vue'

export default{
name:"ProSelect",
components:{
ViewBox
},
data:function(){
return {
vuegConfig:{
forwardAnim:'slideInRight',  //options所有配置可以写在这个对象里，会覆盖全局的配置
disable:false,//对于嵌套路由，默认为关闭动画，需要在组件的data.vuegConfig中配置disable为false启用
backAnim:'slideInLeft'
},

SchoolArr1: [],
school:true,
noData:false
}
},
mounted(){

var _this=this
this.$http.get("https://www.xueguoguo.cn/api/v1/colleges/recommandMajors?college_id="+this.$store.state.VolId,{})
.then(function(result){

if(result.data.msg=="没有数据"){
_this.school=false
_this.noData=true
}else{
_this.school=true
_this.noData=false
_this.$nextTick(function(){
_this.draw();
})
}
})


},
methods:{

draw() {
$(".myChart").each(function() {
$(this).ringChart();
})
}
}
}



