



import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import Zepto from '../../static/zepto.min.js'


export default{
directives: {
TransferDom
},
name:'Mark',
components:{
XHeader,
Picker,
Cell,
Toast,
Confirm,
Loading,
ViewBox

},
data:function(){
return {
vuegConfig:{
forwardAnim:'bounceInUp',  //options所有配置可以写在这个对象里，会覆盖全局的配置
disable:false,//对于嵌套路由，默认为关闭动画，需要在组件的data.vuegConfig中配置disable为false启用
backAnim:'bounceInDown'
},
flag:"true",
chooseData:["高考分数","全省排行"],
data:"分数",
mark:this.$store.state.mark,
ranking:this.$store.state.ranking,
fromPlace:this.$store.state.fromPlace,
subject:this.$store.state.subject,
slots:[this.$store.state.position],
maskStyle:{
height:""
},
sub:[["文科","理科"]],
transfer:{

subject:[""],
fromPlace:[""]
},
position: 'default',
showPositionValue:false,
show3:false,
textTitle:"",

showErrormark:false,
showErrorranking:false,
showLoad:false



}
},
mounted(){
var _this=this

this.$http.get("https://www.xueguoguo.cn/api/v1/aspiration/detail?token="+this.$store.state.token,{
}).then(function(response){

_this.mark=response.data.data.score;
_this.subject=response.data.data.examType
_this.fromPlace=response.data.data.province
_this.ranking=response.data.data.rank
_this.$store.state.mark=response.data.data.score;
_this.$store.state.subject=response.data.data.examType
_this.$store.state.fromPlace=response.data.data.province
_this.$store.state.ranking=response.data.data.rank
_this.$store.state.sex=response.data.data.sex_rate
_this.$store.state.school=response.data.data.college_province
_this.$store.state.occ=response.data.data.job_categories
_this.$store.state.pro=response.data.data.major_categories

});






this.maskStyle.height=document.documentElement.clientHeight*2/100+"rem";
if(this.$store.state.fromPlace==""){

this.transfer.fromPlace[0]=this.slots[0][0]
}else{
this.transfer.fromPlace[0]=this.$store.state.fromPlace
}
if(this.$store.state.subject==""){
this.transfer.subject[0]=this.sub[0][0]
}else{
this.transfer.subject[0]=this.$store.state.subject
}

},


methods:{
onConfirm(value){
if(this.data=="分数"){

if(parseInt(value)>750||parseInt(value)<0){
this.showErrormark=true;

}else{
this.$store.state.mark=value;
this.mark=this.$store.state.mark
}


}else{

if(parseInt(value)<0){
this.showErrorranking=true;

}else if(parseInt(value)>=10000){
this.$store.state.ranking=">9999"
this.ranking=this.$store.state.ranking
}else{
this.$store.state.ranking=value;

this.ranking=this.$store.state.ranking
}



}

},

placeShow(){
$("#place").fadeIn()
},
subShow(){
$("#subject").fadeIn()
},
choose(){
if(this.flag){
$(".select_btn").animate({"height":"1.41rem"})
$(".pull_down").animate({"transform":"rotate(180deg)"})
}else{
$(".select_btn").animate({"height":"0.7rem"});
$(".pull_down").animate({"transform":"rotate(0deg)"})
}

this.flag=!this.flag
}
,
choose_type(index,data){

if(this.flag==false){
if(index==1){
var temp=$('.select_btn li').eq(0).html();
$('.select_btn li').eq(0).html($('.select_btn li').eq(1).html());
$('.select_btn li').eq(1).html(temp);

if(temp=="高考分数"){
$(".mark_box span").html("名");
this.data="名次"

}else{
$(".mark_box span").html("分");
this.data="分数"
}
}

}


}
,
openMessage(){
$(".weui-toast").css("height","1rem")
this.show3=true
if(this.data=="分数"){
this.textTitle="请输入您的高考分数"

}else{
this.textTitle="请输入您的高考排名"

}

},


placeChange(value){


this.transfer.fromPlace[0]=value[0];
},
subChange(value){

this.transfer.subject[0]=value[0];
},
closePlace(){
$('#place').fadeOut()
},
comfirmPlace(){
this.$store.state.fromPlace=this.transfer.fromPlace[0]
this.fromPlace=this.$store.state.fromPlace;
$('#place').fadeOut()
}
,
closeSub(){
$('#subject').fadeOut()
},
comfirmSub(){
this.$store.state.subject=this.transfer.subject[0]
this.subject=this.$store.state.subject;
$('#subject').fadeOut()
},
comfirm(position){
$(".weui-toast").css("height","1rem")
if(this.$store.state.mark==""){


this.position = position
this.showPositionValue=true
return;
}
else if(this.$store.state.subject==""){
this.position = position
this.showPositionValue=true
return;
}
else if(this.$store.state.fromPlace==""){
this.position = position
this.showPositionValue=true
return;
}
else{
$.ajax({
type:"post",
url:"https://www.xueguoguo.cn/api/v1/aspiration/update?token="+this.$store.state.token,
async:false,
data: "{\n        \"id\": 29,\n        \"score\": "+this.mark+",\n        \"rank\": "+this.ranking+",\n        \"province\": \""+this.fromPlace+"\",\n        \"examType\": \""+this.subject+"\",\n        \"college_province\": \""+this.$store.state.school+"\",\n        \"sex_rate\": \""+this.$store.state.sex+"\",\n        \"major_categories\": \""+this.$store.state.pro+"\",\n        \"job_categories\": \""+this.$store.state.occ+"\",\n        \"unit\": \"分\"\n }"

}).done(function(result){
console.log(result)
});
this.$http.get("https://www.xueguoguo.cn/api/v1/aspiration/detail?token="+this.$store.state.token, {}).then(function(response) {
console.log(response)
_this.$store.state.mark=response.data.data.score;
_this.$store.state.subject=response.data.data.examType
_this.$store.state.fromPlace=response.data.data.province
_this.$store.state.ranking=response.data.data.rank
_this.$store.state.sex=response.data.data.sex_rate
_this.$store.state.school=response.data.data.college_province
_this.$store.state.occ=response.data.data.job_categories
_this.$store.state.pro=response.data.data.major_categories

});



$(".weui-toast").css("min-height","2.5rem")
this.showLoad=true;
var _this=this
setTimeout(function(){
_this.showLoad=false;
if(_this.$store.state.Inflag){
_this.$router.push({path:'/Analog'})
}else{
_this.$store.state.Inflag=true
_this.$router.go(-1);
_this.$router.isBack = true;

}


},800)



}

}
}
}




