
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'

import Zepto from '../../static/zepto.min.js'
import Highcharts from '../../static/highcharts.js'
import Progress from '../../static/progress.js'


const list = () => ['录取', '简介', '专业']
export default{
name:"School"
,
components: {

Tab,
TabItem,
Swiper,
SwiperItem,
XImg,
ViewBox

},

data:function(){
return {
vuegConfig:{
forwardAnim:'slideInRight',  //options所有配置可以写在这个对象里，会覆盖全局的配置
disable:false,//对于嵌套路由，默认为关闭动画，需要在组件的data.vuegConfig中配置disable为false启用
backAnim:'slideInLeft'
},
SchoolName:"",
SchoolRank:"",
tags:[],
list2: list(),
index: this.$store.state.SchoolIndex,
demo2: '录取',
per:0,
lowest:0,
title:"",
year:"",
grade_years:[],
grade_text:["录取最低分", "录取平均分"],
province:"",
examType:"",
collegeLineName:[],
arr_grade:[],
batchFlag:true,
gradeFlag:true,
marjorFlag:true,
first:{
firstYear:[],
firstlowests:[],
firstaverages:[]
},
second:{
secondYear:[],
secondlowests:[],
secondaverages:[]
},
junior:{
juniorYear:[],
juniorlowests:[],
junioraverages:[]
},
marjorArr:[],
batchSecond:[],
newBatchFlag:true,

marjorMark:[


],
firstMarjor:{
subject:[],
mark:[]
},
secondMarjor:{
subject:[],
mark:[]
},
thirdMarjor:{
subject:[],
mark:[]
},
SchoolData:{
schoolUrl:"",
schoolPhone:0,
schoolContent:"",
schoolText:true,
schoolPosition:"",
schoolImg:[]
},
proArr:["交通输入类","公共管理类","化学类","建筑类","工商管理类","工科实验班类","交通输入类","公共管理类","化学类","建筑类","工商管理类","工科实验班类"],
is985:false,
is211:false,
firstLine:false,
secondLine:false,
background:"",
categoryArr:[]

}
},
mounted(){


console.log(this.$store.state.id)
var _this=this;
this.$http.get("https://www.xueguoguo.cn/api/v1/colleges/detail/?token="+this.$store.state.token+"&id="+this.$store.state.id,{}).then(function(result){
console.log(result.data)
_this.SchoolName=result.data.data.name
_this.tags=result.data.data.tags
_this.SchoolRank=result.data.data.rank
_this.background=result.data.data.photo

if(_this.background==""){
$(".School_top_box").css("background","#2B94FA")
}


if(result.data.data.is985=="1"){
_this.is985=true
}else{
_this.is985=false
}
if(result.data.data.is211=="1"){
_this.is211=true
}else{
_this.is211=false
}
if(result.data.data.batch=="一本"){
_this.firstLine=true
}else if(result.data.data.batch=="二本"){
_this.secondLine=false
}


})




this.$http.get("https://www.xueguoguo.cn/api/v1/colleges/luqu/"+this.$store.state.id+"?token="+this.$store.state.token,{

}).then(function(result){

if(result.data.data.advice) {
_this.per=Math.floor(result.data.data.advice.posibility*100)
_this.title=result.data.data.advice.desc
_this.lowest=result.data.data.advice.lowest
_this.year=result.data.data.advice.year
_this.$nextTick(function() {
_this.draw();

})

} else {
$('#advice_box').hide();
$(".line").eq(1).hide();
}



_this.province=result.data.data.province;
_this.examType=result.data.data.examType;
_this.grade_str=_this.grade_text[0];

for(var i=0;i<result.data.data.collegeLine.length;i++){
if(result.data.data.collegeLine[i].years=="" || result.data.data.collegeLine[i].lowests=="" || result.data.data.collegeLine[i].averages==""){

result.data.data.collegeLine.splice(i,1)

}
}

for(var i=0;i<result.data.data.collegeLine.length;i++){
if(result.data.data.collegeLine[i].name=="本科一批"){
_this.collegeLineName.push(result.data.data.collegeLine[i].name);
break;
}
}
for(var i=0;i<result.data.data.collegeLine.length;i++){
if(result.data.data.collegeLine[i].name=="本科二批"){
_this.collegeLineName.push(result.data.data.collegeLine[i].name);
break;
}
}

for(var i=0;i<result.data.data.collegeLine.length;i++){
if(result.data.data.collegeLine[i].name=="专科"){
_this.collegeLineName.push(result.data.data.collegeLine[i].name);
break;
}
}




for(var i=0;i<result.data.data.collegeLine.length;i++){
if(result.data.data.collegeLine[i].name=="本科一批"){
_this.first.firstYear=result.data.data.collegeLine[i].years.split(",").reverse();
_this.first.firstlowests=result.data.data.collegeLine[i].lowests.split(",").reverse();
_this.first.firstaverages=result.data.data.collegeLine[i].averages.split(",").reverse();
}else if(result.data.data.collegeLine[i].name=="本科二批"){
_this.second.secondYear=result.data.data.collegeLine[i].years.split(",").reverse();
_this.second.secondlowests=result.data.data.collegeLine[i].lowests.split(",").reverse();
_this.second.secondaverages=result.data.data.collegeLine[i].averages.split(",").reverse();
}else{
_this.junior.juniorYear=result.data.data.collegeLine[i].years.split(",").reverse();
_this.junior.juniorlowests=result.data.data.collegeLine[i].lowests.split(",").reverse();
_this.junior.junioraverages=result.data.data.collegeLine[i].averages.split(",").reverse();
}
}


if(_this.first.firstlowests.length!=0){
_this.arr_grade=_this.first.firstlowests;

}else if(_this.second.secondlowests.length!=0){
_this.arr_grade=_this.second.secondlowests;

}else{
_this.arr_grade=_this.junior.juniorlowests;
}


if(_this.first.firstlowests.length!=0){
_this.grade_years=_this.first.firstYear;

}else if(_this.second.secondlowests.length!=0){
_this.grade_years=_this.second.secondYear;

}else{
_this.grade_years=_this.junior.juniorYear;
}

for(var i=0;i<result.data.data.majorLine.length;i++){
_this.marjorArr.push(result.data.data.majorLine[i].year)
}

_this.marjorArr=_this.marjorArr.reverse()

for(var i=0;i<result.data.data.majorLine.length;i++){
_this.batchSecond.push(result.data.data.majorLine[i].batch)
}

_this.batchSecond=_this.unique1(_this.batchSecond)

for(var i=0;i<result.data.data.majorLine.length;i++){
if(result.data.data.majorLine[i].year=="2016"){
_this.firstMarjor.subject=result.data.data.majorLine[i].majors.split(",");
_this.firstMarjor.mark=result.data.data.majorLine[i].lowests.split(",");

}else if(result.data.data.majorLine[i].year=="2015"){
_this.secondMarjor.subject=result.data.data.majorLine[i].majors.split(",");
_this.secondMarjor.mark=result.data.data.majorLine[i].lowests.split(",");

}else{
_this.thirdMarjor.subject=result.data.data.majorLine[i].majors.split(",");
_this.thirdMarjor.mark=result.data.data.majorLine[i].lowests.split(",");

}
}
for(var i=0;i<_this.firstMarjor.subject.length;i++){
var obj={subject:"",mark:""}
obj.subject=_this.firstMarjor.subject[i]
obj.mark=_this.firstMarjor.mark[i]
_this.marjorMark.push(obj)
}

if(_this.$store.state.SchoolIndex==0){
_this.$nextTick(function() {

_this.$refs.swiper.xheight = 15.7+_this.marjorMark.length*0.7+"rem"
})
}else if(_this.$store.state.SchoolIndex==1){
_this.$nextTick(function() {

_this.$refs.swiper.xheight = 15.4+"rem"
})
}else{



_this.$nextTick(function() {

_this.$refs.swiper.xheight = 10+"rem"
})
}



console.log(_this.firstMarjor)




_this.arr_num(_this.arr_grade)
_this.drawCart();



})


},
methods:{
back() {
this.$router.go(-1);

},
arr_num(arr) {
var len = arr.length;
for(var i = 0; i < len; i++) {
arr[i] = Number(arr[i]);

}
return arr;
},
successLoad (src, ele) {

const img = ele.parentNode.querySelector('img')
ele.parentNode.removeChild(img)
},
goMorePhoto(){
this.$store.state.SchoolIndex=1
this.$router.push({
path: '/MorePhoto'
})
},
batch(){
if(this.collegeLineName.length>1){
if(this.batchFlag){
console.log(1)
$("#admit_batch").animate({"height":0.55*this.collegeLineName.length+"rem"})
this.batchFlag=false
}else{
$("#admit_batch").animate({"height":"0.55rem"})
this.batchFlag=true
}
}


},
newBatch(){
if(this.batchSecond.length>1){
if(this.newBatchFlag){

$("#admit_first").animate({"height":0.55*this.collegeLineName.length+"rem"})
this.newBatchFlag=false
}else{
$("#admit_first").animate({"height":"0.55rem"})
this.newBatchFlag=true
}
}
},
selectBatch(index,name){

if(this.newBatchFlag==false){
if(index==1){
var temp=$("#admit_first li").eq(0).children("li span").html()
$("#admit_first li").eq(0).children("li span").html($("#admit_first li").eq(1).children("li span").html())
$("#admit_first li").eq(1).children("li span").html(temp)

}
}


},
grade(){

if(this.gradeFlag){
console.log(1)
$("#admit_grade").animate({"height":"1.1rem"})
this.gradeFlag=false
}else{
$("#admit_grade").animate({"height":"0.55rem"})
this.gradeFlag=true
}



},
marjor(){
if(this.marjorArr.length>1){
if(this.marjorFlag){
console.log(1)
$("#admit_year").animate({"height":0.55*this.marjorArr.length+"rem"})
this.marjorFlag=false
}else{
$("#admit_year").animate({"height":"0.55rem"})
this.marjorFlag=true
}
}
},
marjor_text(index,name){

if(this.marjorFlag==false){
if(index==1){
var temp=$("#admit_year li").eq(0).children("li span").html()
$("#admit_year li").eq(0).children("li span").html($("#admit_year li").eq(1).children("li span").html())
$("#admit_year li").eq(1).children("li span").html($("#admit_year li").eq(2).children("li span").html())
$("#admit_year li").eq(2).children("li span").html(temp);

}else if(index==2) {
var temp=$("#admit_year li").eq(0).children("li span").html();
$("#admit_year li").eq(0).children("li span").html($("#admit_year li").eq(2).children("li span").html())
$("#admit_year li").eq(2).children("li span").html($("#admit_year li").eq(1).children("li span").html());
$("#admit_year li").eq(1).children("li span").html(temp)


}
console.log($("#admit_year li").eq(0).children("li span").html())
this.updataMajor($("#admit_year li").eq(0).children("li span").html())
this.$nextTick(function() {

this.$refs.swiper.xheight = 15.7+this.marjorMark.length*0.7+"rem"
})
}
},
batch_text(index,name){
if(this.batchFlag==false){
if(index==1){
var temp=$("#admit_batch li").eq(0).children("li span").html()
$("#admit_batch li").eq(0).children("li span").html($("#admit_batch li").eq(1).children("li span").html())
$("#admit_batch li").eq(1).children("li span").html(temp)
this.updata($("#admit_batch li").eq(0).children("li span").html(),$("#admit_grade li").eq(0).children("li span").html())
this.drawCart()
}
}

},	gradeText(index,name){
if(this.gradeFlag==false){
if(index==1){
var temp=$("#admit_grade li").eq(0).children("li span").html()
$("#admit_grade li").eq(0).children("li span").html($("#admit_grade li").eq(1).children("li span").html())
$("#admit_grade li").eq(1).children("li span").html(temp)
this.updata($("#admit_batch li").eq(0).children("li span").html(),$("#admit_grade li").eq(0).children("li span").html())
this.drawCart()
}
}

}

,draw() {
$(".myChart").each(function() {
$(this).ringChart();
})
},
drawCart(){
var _this=this;
console.log(_this.grade_years)
console.log( _this.grade_str)
console.log(_this.arr_grade)
Highcharts.chart('container', {

credits: {
enabled: false
},

title: {
text: null
},
xAxis: {

gridLineWidth: 0,
categories: _this.grade_years
},

yAxis: [{ // left y axis
gridLineWidth: 0,
lineWidth: 1,
title: null,

tickWidth: 1,
tickLength: 10,
showFirstLabel: false,

}],

legend: {
enabled: false
},

tooltip: {
shared: true,
crosshairs: false
},

plotOptions: {
series: {
cursor: 'pointer',

marker: {
lineWidth: 1
}
}

},

series: [{
name: _this.grade_str,
data: _this.arr_grade,
lineWidth: 2,
marker: {
radius: 4,
lineColor: "#2B94FA",
fillColor: '#2B94FA',
}
}],
responsive: {
rules: [{

condition: {
maxWidth: 600
},
chartOptions: {
legend: {
verticalAlign: 'bottom',
y: 0,
floating: false
}
}
}]
}
});
},
updata(left,right){
if(left=="本科一批"){
if(right=="录取最低分"){
this.arr_grade=this.first.firstlowests;
}else{
this.arr_grade=this.first.firstaverages;
}
this.grade_years=this.first.firstYear;
}else if(left=="本科二批"){
if(right=="录取最低分"){


this.arr_grade=this.second.secondlowests;
console.log(this.arr_grade)
}else{
this.arr_grade=this.second.secondaverages;
}
this.grade_years=this.second.secondYear;
}else{
if(right=="录取最低分"){
this.arr_grade=this.junior.juniorlowests;
}else{
this.arr_grade=this.junior.junioraverages;
}
this.grade_years=this.junior.juniorYear;
}

this.arr_num(this.arr_grade)
},
updataMajor(left){
this.marjorMark.length=0;
if(left=="2016"){
for(var i=0;i<this.firstMarjor.subject.length;i++){
var obj={subject:"",mark:""}
obj.subject=this.firstMarjor.subject[i]
obj.mark=this.firstMarjor.mark[i]
this.marjorMark.push(obj)
}

}else if(left=="2015"){
for(var i=0;i<this.secondMarjor.subject.length;i++){
var obj={subject:"",mark:""}
obj.subject=this.secondMarjor.subject[i]
obj.mark=this.secondMarjor.mark[i]
this.marjorMark.push(obj)
}

}else{
for(var i=0;i<this.thirdMarjor.subject.length;i++){
var obj={subject:"",mark:""}
obj.subject=this.thirdMarjor.subject[i]
obj.mark=this.thirdMarjor.mark[i]
this.marjorMark.push(obj)
}

}

console.log(this.marjorMark)
},


unique1(arr) {
var res = [arr[0]];
for(var i = 1; i < arr.length; i++) {
var repeat = false;
for(var j = 0; j < res.length; j++) {
if(arr[i] == res[j]) {
repeat = true;
break;
}
}
if(!repeat) {
res.push(arr[i]);
}
}
return res;
},
change(currentIndex){
var _this=this
this.$store.state.SchoolIndex=currentIndex
console.log(this.$store.state.SchoolIndex)
if(currentIndex==0){

this.$nextTick(function() {

this.$refs.swiper.xheight = 15.7+this.marjorMark.length*0.7+"rem"
})

}


else if(currentIndex==1){
this.decide()
this.$http.get("https://www.xueguoguo.cn/api/v1/colleges/brief/" + this.$store.state.id,{}).then(function(result){
console.log(result.data)
_this.SchoolData.schoolUrl=result.data.data.website;
_this.SchoolData.schoolPhone=result.data.data.phone;
_this.SchoolData.schoolContent=result.data.data.intro;
if(_this.SchoolData.schoolContent.indexOf("\n") != -1) {
console.log(1)
var new_text_arr = _this.SchoolData.schoolContent.split("\n");
var new_text = "";
for(var k = 0; k < new_text_arr.length; k++){
if(k > 0) {
new_text_arr[k] = "<br/>&nbsp;&nbsp;&nbsp;&nbsp;" + new_text_arr[k]
}
new_text += new_text_arr[k]
}
_this.SchoolData.schoolContent="&nbsp;&nbsp;&nbsp;&nbsp;"+new_text;
console.log(_this.SchoolData.schoolContent.length)
_this.SchoolData.schoolPosition=result.data.data.city;
$(".scale_line_left").css("width", 100 -result.data.data.sexRate + "%");
$(".scale_line_right").css("width", result.data.data.sexRate + "%");
$(".scale_box span").eq(0).html((100 - result.data.data.sexRate));
$(".scale_box span").eq(2).html((result.data.data.sexRate));
if(result.data.data.photos.indexOf(",")) {
var imgArr = result.data.data.photos.split(",");
for(var i = 0; i < imgArr.length; i++) {
if(imgArr[i].indexOf("http") == -1) {
imgArr[i] = "http://" + imgArr[i]}}
_this.SchoolData.schoolImg.length=0
_this.SchoolData.schoolImg.push(imgArr[0]);
_this.SchoolData.schoolImg.push(imgArr[1]);
_this.SchoolData.schoolImg.push(imgArr[2]);
console.log(_this.SchoolData.schoolImg)
$("img").error(function(e) {
$(this).attr("src", "../assets/img1.png");
$(this).css({
"width": "2.18rem",
"height": "1.47rem"})});
} else {



$('.img_box').hide()
$(".summary line").eq(1).hide()
}



}
})
}else{

this.$http.get("https://www.xueguoguo.cn/api/v1/colleges/majorCategories?college_id="+this.$store.state.id,{})
.then(function(result){
console.log(result.data)
_this.categoryArr=result.data.data
})





this.$nextTick(function() {

this.$refs.swiper.xheight = 10+"rem"
})
}


},
more(){
if(this.SchoolData.schoolText){

$("#school_about").removeClass("line_unbreak");
var aboutHeight=$("#school_about").height()*2/100-1.28
console.log(aboutHeight)
this.$nextTick(function() {

this.$refs.swiper.xheight = 15.4+aboutHeight+"rem"
})
$(".check_all img").css({
"transform": "rotate(270deg)"
});
this.SchoolData.schoolText=false
}else{
$("#school_about").addClass("line_unbreak");
this.$nextTick(function() {

this.$refs.swiper.xheight = 15.4+"rem"
})
$(".check_all img").css({
"transform": "rotate(90deg)"
})
this.SchoolData.schoolText=true
}
},
decide(){

if(this.SchoolData.schoolText){
this.$refs.swiper.xheight = 15.4+"rem"

}else{
var aboutHeight=$("#school_about").height()*2/100-1.28;
this.$nextTick(function() {

this.$refs.swiper.xheight = 15.4+aboutHeight+"rem"
})
}

},

goEach(name){
this.$store.state.ProName=name;
this.$store.state.SchoolIndex=2
console.log(this.$store.state.ProName);
this.$router.push({
path: '/ProSelect'
})
}



},
destroyed() {
document.documentElement.scrollTop=0

}
}



