
import ViewBox from 'vux/src/components/view-box/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default{
name:"Login",
components:{
ViewBox,
Toast,
XHeader
},
data(){
return {
vuegConfig:{
forwardAnim:'bounceInUp',  //options所有配置可以写在这个对象里，会覆盖全局的配置
disable:false,//对于嵌套路由，默认为关闭动画，需要在组件的data.vuegConfig中配置disable为false启用
backAnim:'bounceInDown'
},
showErrormark:false,
success:false
}
},
mounted(){

},
methods:{
back(){
this.$router.go(-1);
},
comfirm(){
var _this=this
var obj={
phone:this.$refs.name.value,
password:this.$refs.password.value

}
$.ajax({
type:"post",
url:"https://www.xueguoguo.cn/api/v1/user/login",
async:false,
data:JSON.stringify(obj)
}).done(function(result){
if(result.msg=="账号或密码错误"){
_this.showErrormark=true
}else{
_this.success=true
console.log(result)
_this.$store.state.token=result.data.token
_this.$store.state.isVip=result.data.isvip

_this.setCookie("name",JSON.stringify(obj))

setTimeout(function(){
_this.$router.go(-1);
},1200)
}


})



},
setCookie(name,value){

var Days = 30;
var exp = new Date();
exp.setTime(exp.getTime() + Days*24*60*60*1000);

document.cookie = name + "="+ escape (value) + ";expires=" + exp.toGMTString();
},
download(){
window.location.href="https://www.xueguoguo.cn/jump.html"
}
}
}



