
import ViewBox from 'vux/src/components/view-box/index.vue'

export default{
name:"JobType",
components:{
ViewBox
},
data(){
return {
vuegConfig:{
forwardAnim:'slideInLeft',
disable:false,
backAnim:'slideInRight',


},
Job:[],
data:true,
noData:false
}
},
mounted(){
var _this=this;
this.$http.get("https://www.xueguoguo.cn/api/v1/job/queryByCategory2?category2="+this.$store.state.JobTitle,{}).then(function(result){
if(result.data.msg=="没有数据"){
_this.noData=true
_this.data=false
}else{
_this.Job=result.data.data
_this.noData=false
_this.data=true
}

})
},
methods:{
goDetail(name,id){
this.$store.state.JobDetailTitle=name
this.$store.state.JobId =id
this.$router.push({
path: '/JobDetail'
})
}
}
}



