

import Zepto from '../../static/zepto.min.js'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default{
directives: {
TransferDom
},
name:"Wish",
components:{

Picker,
Cell,
Toast,
Confirm,
Loading,
XHeader,
ViewBox

},
data:function(){
return{
vuegConfig:{
forwardAnim:'slideInRight',  //options所有配置可以写在这个对象里，会覆盖全局的配置
disable:false,//对于嵌套路由，默认为关闭动画，需要在组件的data.vuegConfig中配置disable为false启用
backAnim:'slideInLeft'
},

school:this.$store.state.school,
sex:this.$store.state.sex,

sexData:[['不限', '男生多', '女生多', '男女平均']],
schoolData:[this.$store.state.position],
maskStyle:{
height:""
},
transfer:{

sex:[""],
school:[""]
},
ProLen:"已选0个专业",
OccLen:"已选0个方向",
showLoad:false
}
},
mounted(){
this.schoolData[0].unshift("不限")

this.maskStyle.height=document.documentElement.clientHeight*2/100+"rem";
this.sexWidth();

if(this.$store.state.school==""){

this.transfer.school[0]=this.schoolData[0][0]
}else{
this.transfer.school[0]=this.$store.state.school
}
if(this.$store.state.sex==""){
this.transfer.sex[0]=this.sexData[0][0]
}else{
this.transfer.sex[0]=this.$store.state.sex
}
if(this.$store.state.pro!=""){
this.ProLen="已选"+this.$store.state.pro.split(",").length+"个专业"
}
if(this.$store.state.occ!=""){
this.OccLen="已选"+this.$store.state.occ.split(",").length+"个专业"
}

},


methods:{

comfirm(){
if(this.$store.state.school=="不限"){
this.$store.state.school=""
}
if(this.$store.state.sex=="不限"){
this.$store.state.sex=""
}
$.ajax({
type:"post",
url:"https://www.xueguoguo.cn/api/v1/aspiration/update?token="+this.$store.state.token,
async:false,
data: "{\n        \"id\": 29,\n        \"score\": "+this.$store.state.mark+",\n        \"rank\": "+this.$store.state.ranking+",\n        \"province\": \""+this.$store.state.fromPlace+"\",\n        \"examType\": \""+this.$store.state.subject+"\",\n        \"college_province\": \""+this.$store.state.school+"\",\n        \"sex_rate\": \""+this.$store.state.sex+"\",\n        \"major_categories\": \""+this.$store.state.pro+"\",\n        \"job_categories\": \""+this.$store.state.occ+"\",\n        \"unit\": \"分\"\n }"

}).done(function(result){

});
this.$http.get("https://www.xueguoguo.cn/api/v1/aspiration/detail?token="+this.$store.state.token, {}).then(function(response) {
console.log(response)
})
this.$store.state.AllWish=0;
if(this.$store.state.school!=""){
this.$store.state.AllWish+=1
}
if(this.$store.state.pro!=""){
this.$store.state.AllWish+=1
}
if(this.$store.state.occ!=""){
this.$store.state.AllWish+=1
}
if(this.$store.state.sex!=""){
this.$store.state.AllWish+=1
}

$(".weui-toast").css("width","2.5rem")
$(".weui-toast").css("min-height","2.5rem")
this.showLoad=true;
var _this=this
setTimeout(function(){
_this.showLoad=false;
_this.$router.push({path:'/Analog'})
},800)



},
goPro(){
this.$router.push({path:'/Professional'})
},
goOcc(){
this.$router.push({path:'/Occupation'})
},
schoolShow(){
$("#school").fadeIn()
},
closeSchool(){
$("#school").fadeOut()
},
comfirmSchool(){
this.$store.state.school=this.transfer.school[0];
this.school=this.$store.state.school;
$("#school").fadeOut()

},
schoolChange(value){
this.transfer.school[0]=value[0];
},
sexShow(){
$("#sex").fadeIn()
},
closeSex(){
$("#sex").fadeOut()
},
comfirmSex(){
this.$store.state.sex=this.transfer.sex[0];
this.sex=this.$store.state.sex;
this.sexWidth()
$("#sex").fadeOut()

},
sexChange(value){

this.transfer.sex[0]=value[0];
},
sexWidth(){
if(this.sex=="不限"){
$(".sexBox .select").css("width","1rem")
}else{
$(".sexBox .select").css("width","1.2rem")
}
}


}
}



