
import Zepto from '../../static/zepto.min.js'
import Progress from '../../static/progress.js'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


let Analog_swiper;
export default {
directives: {
TransferDom
},
name: "Analog",
components: {
Loading,
Tab,
TabItem,
Swiper,
SwiperItem,
AlertModule,
Alert,
XHeader,
ViewBox
},
data: function() {
return {
vuegConfig:{
forwardAnim:'slideInRight',  //options所有配置可以写在这个对象里，会覆盖全局的配置
disable:false,//对于嵌套路由，默认为关闭动画，需要在组件的data.vuegConfig中配置disable为false启用
backAnim:'slideInLeft'
},

mark: this.$store.state.mark,
ranking: this.$store.state.ranking,
fromPlace: this.$store.state.fromPlace,
subject: this.$store.state.subject,

school: this.$store.state.school,
SchoolArr:[],
SchoolArr1: [],
SchoolArr2: [],
scrollTop: false,

index: 0,
Chong:this.$store.state.Chong,
Wen:this.$store.state.Wen,
Bao:this.$store.state.Bao,
demo2: '可冲击 ('+this.$store.state.Chong+')',
list2: [],
Alert: false,

AllWish:this.$store.state.AllWish,
showLoad:false
}
},
mounted() {
var _this=this;


this.$http.get("https://www.xueguoguo.cn/api/v1/colleges/recommand?token="+this.$store.state.token+"&province="+this.$store.state.fromPlace+"&score="+this.$store.state.mark+"&examType="+this.$store.state.subject,{}).then(function(result){

for(var i=0;i<result.data.data.length;i++){
if(result.data.data[i].category=="可冲击"){
_this.SchoolArr.push(result.data.data[i])
}else if(result.data.data[i].category=="较稳妥"){
_this.SchoolArr1.push(result.data.data[i])
}else if(result.data.data[i].category=="可保底"){
_this.SchoolArr2.push(result.data.data[i])
}
}

_this.$store.state.Chong=_this.SchoolArr.length;
_this.$store.state.Wen=_this.SchoolArr1.length;
_this.$store.state.Bao=_this.SchoolArr2.length;


_this.$nextTick(function() {
var height = document.documentElement.clientHeight*2/100-0.88+"rem"
_this.$refs.swiper.xheight = height
_this.draw();
})



_this.changeWidth();

_this.list2.push('可冲击 ('+_this.$store.state.Chong+')', '较稳妥 ('+_this.$store.state.Wen+')', '可保底 ('+_this.$store.state.Bao+')')

})







},
methods: {

draw() {

$(".myChart").each(function() {
$(this).ringChart();
})
},


toMark() {
this.$router.push({
path: '/Mark'
})
},
toWish() {
this.$router.push({
path: '/Wish'
})
},
prompt() {
$(".weui-dialog").css({
"height": "3.6rem",
"width": "5rem"
})
this.Alert = true
},

goSchool(id) {

this.$store.state.id =id
this.$router.push({
path: '/School'
})
},
changeWidth() {
if(this.ProLen + this.OccLen > 10) {

$("#wish").css("width", "2.8rem")
}
},
change(currentIndex) {
$(".weui-toast").css("min-height","2.5rem")
var _this=this
this.showLoad=true
if(currentIndex == 1) {
setTimeout(function(){
_this.$nextTick(function() {

var height = document.documentElement.clientHeight*2/100-0.88+"rem"
_this.$refs.swiper.xheight = height
})
_this.showLoad=false
},500)

} else if(currentIndex == 2) {
setTimeout(function(){
_this.$nextTick(function() {

var height =  document.documentElement.clientHeight*2/100-0.88+"rem"
_this.$refs.swiper.xheight = height
})
_this.showLoad=false
},500)

} else {
setTimeout(function(){
_this.$nextTick(function() {

var height =  document.documentElement.clientHeight*2/100-0.88+"rem"
_this.$refs.swiper.xheight = height
})
_this.showLoad=false
},500)

}
}

}

}



