
import Search from 'vux/src/components/search/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'

export default {
name: "SearchSchool",
components: {
Search,
Scroller,
ViewBox
},
data() {
return {
vuegConfig:{
forwardAnim:'slideInRight',  //options所有配置可以写在这个对象里，会覆盖全局的配置
disable:false,//对于嵌套路由，默认为关闭动画，需要在组件的data.vuegConfig中配置disable为false启用
backAnim:'slideInLeft'
},
results: [],
value: this.$store.state.SchoolInput,

history: false,
search: false,
Null: false,
searchArr: [],
index: 0,
historyArr: [],
pullupConfig2: {
content: '上拉加载更多',
downContent: '松开进行加载',
upContent: '上拉加载更多',
loadingContent: '加载中...'
},
pulldownConfig2: {
content: '下拉刷新',
downContent: '下拉刷新',
upContent: '释放刷新',
loadingContent: '加载中...'
},

noData: false,
noDataFlag: false,
status1: {
pulldownStatus: 'default'
}
}
},
mounted() {
if(window.localStorage.getItem("School") == undefined) {
window.localStorage.setItem("School", "[]")
} else {
this.historyArr = JSON.parse(window.localStorage.getItem("School"));

}


if(this.$store.state.SchoolInput == "") {
this.history = true;
this.search = false
} else {
this.history = false;
this.search = true;
this.getData()

this.$nextTick(() => {
this.$refs.demo2.reset()

})

}

$('.vux-search-box').css({"width":"5.8rem","left":"1rem"});
},
methods: {
back() {

this.$router.go(-1);

},
delectHistory(index){

this.historyArr.splice(index,1);
window.localStorage.setItem("School",JSON.stringify(this.historyArr))
},
getArr(val,id) {
this.historyArr.push(val);
this.historyArr=this.unique1(this.historyArr);
window.localStorage.setItem("School",JSON.stringify(this.historyArr))
this.$store.state.id =id
this.$router.push({
path: '/School'
})

},

unique1(arr) {
var res = [arr[0]];
for(var i = 1; i < arr.length; i++) {
var repeat = false;
for(var j = 0; j < res.length; j++) {
if(arr[i] == res[j]) {
repeat = true;
break;
}
}
if(!repeat) {
res.push(arr[i]);
}
}
return res;
},
getData() {
var _this = this;
var str = "&keyword=" + this.$store.state.SchoolInput

this.$http.get("https://www.xueguoguo.cn/api/v1/colleges/query?index=" + this.index + "&size=10" + str, {}).then(function(result) {

if(result.data.msg == "没有数据") {
if(_this.searchArr.length > 0) {
_this.noData = false
_this.noDataFlag = true;

_this.$refs.demo2.disablePullup()

} else {
_this.Null = true;
_this.search = false;
}

} else {
_this.history = false;
_this.Null = false;
_this.search = true;
for(var i = 0; i < result.data.data.length; i++) {
_this.searchArr.push(result.data.data[i])
}

}
})
},
getResult(val) {


},
onFocus() {
var _this=this;
$("input").on('keypress',function(e) {

var keycode = e.keyCode;
var searchName = $(this).val();
if(keycode=='13') {

e.preventDefault();


_this.$store.state.SchoolInput=this.value
_this.$router.push({
path: '/SearchSchool'
})
}
});
},
onCancel() {

},
onBlur() {

this.$store.state.SchoolInput = this.value
this.searchArr.length = 0
this.getData()
},
load1() {

setTimeout(() => {

this.status1.pulldownStatus = 'default'
}, 2000)
},
load2() {
setTimeout(() => {
if(this.noDataFlag == false) {
this.index += 1
this.getData()
setTimeout(() => {
this.$refs.demo2.donePullup()
}, 100)
}
if(this.noDataFlag) { // unload plugin
setTimeout(() => {
this.noData = true
this.$refs.demo2.disablePullup()

}, 200)
}
}, 2000)
}
},
activated() {
this.$refs.demo2.reset()

}
}



