
import Checklist from 'vux/src/components/checklist/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'

export default {
name:"TestContent",
components:{

Checklist,
ViewBox
},
data(){
return {
vuegConfig:{
forwardAnim:'slideInRight',
disable:false,
backAnim:'slideInLeft'
},

num:1,
len:"",
testTitle:"",
commonList: [ '是', '否' ],
checklist001:[],
questionArr:[],
changeFlag:true,
r:0,
i:0,
a:0,
s:0,
e:0,
c:0,
one:0,
two:0,
three:0,
four:0,
five:0,
six:0,
seven:0,
eight:0,
nine:0

}
},
mounted(){
var _this=this;


this.$http.get("https://www.xueguoguo.cn/api/v1/ptest/paper?id="+this.$store.state.TestId,{})
.then(function(result){

_this.len=result.data.data.questions.length;
_this.questionArr=result.data.data.questions
_this.testTitle=_this.delect(_this.questionArr[0].title)
_this.num=_this.questionArr[0].id

})

},
methods:{

change(val){
if(val[0]=="是"){
if(this.questionArr[this.num-1].resultID=="r"){
this.r+=1
}else if(this.questionArr[this.num-1].resultID=="i"){
this.i+=1
}else if(this.questionArr[this.num-1].resultID=="a"){
this.a+=1
}else if(this.questionArr[this.num-1].resultID=="s"){
this.s+=1
}else if(this.questionArr[this.num-1].resultID=="e"){
this.e+=1
}else if(this.questionArr[this.num-1].resultID=="c"){
this.c+=1
}
else if(this.questionArr[this.num-1].resultID=="1"){
this.one+=1
}else if(this.questionArr[this.num-1].resultID=="2"){
this.two+=1
}else if(this.questionArr[this.num-1].resultID=="3"){
this.three+=1
} else if(this.questionArr[this.num-1].resultID=="4"){
this.four+=1
}else if(this.questionArr[this.num-1].resultID=="5"){
this.five+=1
}else if(this.questionArr[this.num-1].resultID=="6"){
this.six+=1
}else if(this.questionArr[this.num-1].resultID=="7"){
this.seven+=1
}else if(this.questionArr[this.num-1].resultID=="8"){
this.eight+=1
}else if(this.questionArr[this.num-1].resultID=="9"){
this.nine+=1
}












}

var _this=this
setTimeout(function(){
if(_this.checklist001.length!=0){

_this.checklist001=[]
_this.getData()

}

},400)
},
delect(str){

if(str.indexOf("．  ")!=-1){

var index=str.indexOf("．  ")

var newStr=str.substr(index+1)
return newStr

}else{
return str
}
},
getData(){
if(this.num<Number(this.len)){
this.num+=1
this.testTitle=this.delect(this.questionArr[this.num-1].title)
}else{
var n=Math.max(this.r,this.i,this.a,this.s,this.e,this.c,this.one,this.two,this.three,this.four,this.five,this.six,this.seven,this.eight,this.nine)
if(this.r==n){
this.$store.state.TestResult="r"
}else if(this.i==n){
this.$store.state.TestResult="i"
}else if(this.a==n){
this.$store.state.TestResult="a"
}else if(this.s==n){
this.$store.state.TestResult="s"
}else if(this.e==n){
this.$store.state.TestResult="e"
}else if(this.c==n){
this.$store.state.TestResult="c"
}else if(this.one==n){
this.$store.state.TestResult="1"
}else if(this.two==n){
this.$store.state.TestResult="2"
}else if(this.three==n){
this.$store.state.TestResult="3"
}else if(this.four==n){
this.$store.state.TestResult="4"
}else if(this.five==n){
this.$store.state.TestResult="5"
}else if(this.six==n){
this.$store.state.TestResult="6"
}else if(this.seven==n){
this.$store.state.TestResult="7"
}else if(this.seven==n){
this.$store.state.TestResult="8"
}else{
this.$store.state.TestResult="9"
}

this.$router.push({
path:"/TestResult"
})


}








}
}
}



