
import ViewBox from 'vux/src/components/view-box/index.vue'

export default{
name:"Test",
components:{
ViewBox
},
data(){
return {
vuegConfig:{
forwardAnim:'slideInRight',
disable:false,
backAnim:'slideInLeft'
},
dataArr:[]
}
},
mounted(){
this.getData()
},
methods:{

getData(){
var _this=this
this.$http.get("https://www.xueguoguo.cn/api/v1/ptest/list?",{}).then(function(result){

_this.dataArr=result.data.data
})
},
goDetail(id,title,obj){
if(title.indexOf(":")!=-1){
var index=title.indexOf(":")

title=title.substr(0,index)
}

this.$store.state.TestData=obj
this.$store.state.TestTitle=title
this.$store.state.TestId=id
this.$router.push({
path: '/TestDetail'
})
}
}
}



