
import ViewBox from 'vux/src/components/view-box/index.vue'

export default{
name:"TestDetail",
components:{
ViewBox
},
data(){
return{
vuegConfig:{
forwardAnim:'slideInRight',
disable:false,
backAnim:'slideInLeft'
},
TestData:this.$store.state.TestData,

num:0
}
},
mounted(){
var _this=this;



this.$http.get("https://www.xueguoguo.cn/api/v1/ptest/paper?id="+this.$store.state.TestId,{})
.then(function(result){

_this.num=result.data.data.questions.length

})


},methods:{

goContent(){
this.$router.push({
path:'/TestContent'
})
}
}
}



