
import ViewBox from 'vux/src/components/view-box/index.vue'

export default{
name:"Report",
components:{
ViewBox
},
data(){
return{
vuegConfig:{
forwardAnim:'slideInLeft',
disable:false,
backAnim:'slideInRight',


},
error:true,
com:[],
ti:[],
he:[],
pro:true,
complete:true,
gradient:true,
len:0

}
},
mounted(){

var _this=this
this.$http.get("https://www.xueguoguo.cn/api/v1/simulate/result?id="+this.$store.state.VolData.id+"&token=81C7A6AB-229C-4D7C-BF53-5061FF8C696F",{})
.then(function(result){

_this.len=result.data.data.length
for(var i=0;i<result.data.data.length;i++){
if(result.data.data[i].type=="志愿表完整性"){
_this.com.push(result.data.data[i])
}
if(result.data.data[i].type=="志愿表梯度"){
_this.ti.push(result.data.data[i])
}
if(result.data.data[i].type=="专业设置合理性"){
_this.he.push(result.data.data[i])
}
}
if(_this.he.length==0){
_this.pro=false
}
if(_this.ti.length==0){
_this.gradient=false
}
if(_this.he.length==0){
_this.pro=false
}


})


},
methods:{

}
}



