
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Badge from 'vux/src/components/badge/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Loading from 'vux/src/components/loading/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default{
directives: {
TransferDom
},
name:"VolProSelect",
components:{
ViewBox,
Tabbar,
TabbarItem,
Badge,
Alert,
Loading
},
data(){
return {
vuegConfig:{
forwardAnim:'slideInLeft',
disable:false,
backAnim:'slideInRight',


},
isTrue:true,
imgTrue:true,
imgFalse:true,
textTrue:true,
textFalse:true,
pro:[],
BadgeIndex:0,
show:false,
selectArr:[],
showLoad:false
}
},
mounted(){





$(".weui-dialog").css({"width":"80%","height":"3rem"})
if(this.$store.state.VolType=="本科批次"){
for(var i=0;i<this.$store.state.VolData.data.batchs[0].colleges.length;i++){
if(this.$store.state.VolData.data.batchs[0].colleges[i].item.id==this.$store.state.VolId){
this.isTrue=this.$store.state.VolData.data.batchs[0].colleges[i].adjust
if(this.$store.state.VolData.data.batchs[0].colleges[i].majors!=""){
this.selectArr=this.$store.state.VolData.data.batchs[0].colleges[i].majors.split(",")
this.BadgeIndex=this.selectArr.length
}else{
this.selectArr=[]
this.BadgeIndex=0
}

}
}


}else{
for(var i=0;i<this.$store.state.VolData.data.batchs[1].colleges.length;i++){
if(this.$store.state.VolData.data.batchs[1].colleges[i].item.id==this.$store.state.VolId){
this.isTrue=this.$store.state.VolData.data.batchs[1].colleges[i].adjust
if(this.$store.state.VolData.data.batchs[1].colleges[i].majors!=""){
this.selectArr=this.$store.state.VolData.data.batchs[1].colleges[i].majors.split(",")
this.BadgeIndex=this.selectArr.length
}else{
this.selectArr=[]
this.BadgeIndex=0
}
}
}
}
this.check(this.isTrue)

var _this=this
this.$http.get("https://www.xueguoguo.cn/api/v1/simulate/majors?token="+this.$store.state.token+"&college_id="+this.$store.state.VolId+"&examType="+this.$store.state.mark+"&province="+this.$store.state.fromPlace,{})
.then(function(result){
var arr=result.data.data.majors.split(",")
for(var i=0;i<arr.length;i++){
var obj={

}
obj.text=arr[i]
obj.badge=0
obj.show=false
_this.pro.push(obj)
}


for(var j=0;j<_this.pro.length;j++){
for(var i=0;i<_this.selectArr.length;i++){
if(_this.pro[j].text==_this.selectArr[i]){

_this.pro[j].show=true
_this.pro[j].badge=i+1

}


}
}


})



},
methods:{

onHide(){

},


select(str,index,badge){
if(this.pro[index].show==false){
if(this.BadgeIndex>5){

this.show=true

}else{
this.BadgeIndex++
this.pro[index].badge=this.BadgeIndex
this.pro[index].show=!this.pro[index].show
this.selectArr.push(str)

}

}else{
for(var i=0;i<this.pro.length;i++){
if(this.pro[i].badge>badge){
this.pro[i].badge--
}
}
for(var j=0;j<this.selectArr.length;j++){
if(this.selectArr[j]==str){
this.selectArr.splice(j,1)
}
}

this.BadgeIndex--
this.pro[index].show=!this.pro[index].show
}


},
comfirm(){
$(".weui-toast").css("min-height","2.5rem")
this.showLoad=true

if(this.$store.state.VolType=="本科批次"){
for(var i=0;i<this.$store.state.VolData.data.batchs[0].colleges.length;i++){
if(this.$store.state.VolData.data.batchs[0].colleges[i].item.id==this.$store.state.VolId){
this.$store.state.VolData.data.batchs[0].colleges[i].adjust=this.isTrue
this.$store.state.VolData.data.batchs[0].colleges[i].majors=this.selectArr.join(",")
}
}


}else{
for(var i=0;i<this.$store.state.VolData.data.batchs[1].colleges.length;i++){
if(this.$store.state.VolData.data.batchs[1].colleges[i].item.id==this.$store.state.VolId){
this.$store.state.VolData.data.batchs[1].colleges[i].adjust=this.isTrue
this.$store.state.VolData.data.batchs[1].colleges[i].majors=this.selectArr.join(",")
}
}
}


var _this=this

$.ajax({
type: "post",
url: "https://www.xueguoguo.cn/api/v1/simulate/update?token="+this.$store.state.token,
async: false,
data: JSON.stringify(this.$store.state.VolData)

}).done(function(result) {
setTimeout(function(){
_this.showLoad=false
_this.$router.go(-1);
},500)



});






},
change(){
this.isTrue=!this.isTrue
this.check(this.isTrue)
},
check(isTrue){
if(isTrue){
this.imgTrue=true
this.imgFalse=false
this.textTrue=true
this.textFalse=false
}else{
this.imgTrue=false
this.imgFalse=true
this.textTrue=false
this.textFalse=true
}
}
}
}



