
import ViewBox from 'vux/src/components/view-box/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

const list = () => ['本科','专科']
export default{
name:"SelectPro",
components:{
Search,
Tab,
TabItem,
Swiper,
SwiperItem,
Scroller,
ViewBox
},
data(){
return {
vuegConfig:{
forwardAnim:'slideInRight',
disable:false,
backAnim:'slideInLeft'
},
results: [],
value: this.$store.state.ProSearch,
index:0,
demo2: '本科',
list2: list(),
ProArr:this.$store.state.course_category,
newjonior:this.$store.state.jonior_category,
moreArr:[],
category:"哲学",
jonior_category:"农林牧渔大类",
joniorArr:[],
Data:false,
noData:false,
course:true,
jonior:false
}
},
mounted(){

$(".vux-search-box").css({"width":"5.8rem","left":"1rem"})




$("#course .course").eq(0).css({"background":"#2B94FA","color":"#ffffff"});

$("#jonior .course").eq(0).css({"background":"#2B94FA","color":"#ffffff"});



this.getData()
},
methods:{
back() {
this.$router.go(-1);

},
goDetail(val,id){
this.$store.state.categoryData=val
this.$store.state.categoryId=id

this.$router.push({
path: '/ProDetail'
})
},
changeJonior(index){

$("#jonior .course").eq(index).css({"background":"#2B94FA","color":"#ffffff"}).siblings(".course").css({"background":"#FFFFFF","color":"#1B1E1E"});
var html=$("#jonior .course").eq(index).html()
this.jonior_category=html
this.getData()


},
getResult(val) {


},
getData(){
var _this=this;
this.$http.get("https://www.xueguoguo.cn/api/v1/major/queryByCategory?category="+this.category,{}).then(function(result){

_this.moreArr=result.data.data
_this.$nextTick(function() {
_this.$refs.scroll.reset()
})



})
this.$http.get("https://www.xueguoguo.cn/api/v1/major/queryByCategory?category="+this.jonior_category,{}).then(function(result){

if(result.data.msg=="没有数据"){
_this.noData=true
_this.Data=false
}else{
_this.noData=false
_this.Data=true
_this.joniorArr=result.data.data
_this.$nextTick(function() {
_this.$refs.scrollSecond.reset()
})





}


})




},


onFocus() {

},
onCancel() {

},
onBlur() {


this.$store.state.ProSearch=this.value
this.$router.push({
path: '/SearchPro'
})
},
onItemClick(index){
var _this=this
if(index==0){
this.course=true;
this.jonior=false
this.$nextTick(function() {
this.$refs.scroll.reset()
})

}else{
this.course=false;
this.jonior=true
this.$http.get("https://www.xueguoguo.cn/api/v1/major/queryByCategory?category="+this.jonior_category,{}).then(function(result){

if(result.data.msg=="没有数据"){
_this.noData=true
_this.Data=false
}else{
_this.noData=false
_this.Data=true
_this.joniorArr=result.data.data
_this.$nextTick(function() {
_this.$refs.scrollSecond.reset()
})





}


})




if(this.Data==true){
this.$nextTick(function() {
this.$refs.scrollSecond.reset()
})
}


}
},
changePro(index){
$("#course .course").eq(index).css({"background":"#2B94FA","color":"#ffffff"}).siblings(".course").css({"background":"#FFFFFF","color":"#1B1E1E"});
var html=$("#course .course").eq(index).html()
this.category=html
this.getData()
}
}
}



