
import ViewBox from 'vux/src/components/view-box/index.vue'
import Search from 'vux/src/components/search/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
name: 'SchoolSelect',
components: {
Search,
AlertModule,
Alert,
ViewBox

},
data: function() {
return {
vuegConfig:{
forwardAnim:'slideInRight',  //options所有配置可以写在这个对象里，会覆盖全局的配置
disable:false,//对于嵌套路由，默认为关闭动画，需要在组件的data.vuegConfig中配置disable为false启用
backAnim:'slideInLeft'
},
results: [],
value: this.$store.state.SchoolInput,
level:[{ type:"level",arr:"专科"},{ type:"level",arr:"本科"}]
,
newArr:[{ type:"985",arr:"985"},{ type:"211",arr:"211"}],
arr:this.$store.state.positionArr,
type:this.$store.state.typeArr
,
positionFlag:true,
proFlag:true,
Alert:false
}
},
mounted(){
$(".vux-search-box").css({"width":"5.8rem","left":"1rem"});

this.$http.get("https://www.xueguoguo.cn/api/v1/colleges/query?",{}).then(function(result){

})


},
methods: {
back() {
this.$router.go(-1);

},
goSearch(val,type){
this.$store.state.SchoolSearch=val;
this.$store.state.SchoolSearchType=type;
this.$router.push({
path: '/Allschool'
})
},
getResult(val) {


},
onFocus() {
var _this=this;
$("input").on('keypress',function(e) {

var keycode = e.keyCode;
var searchName = $(this).val();
if(keycode=='13') {

e.preventDefault();


_this.$store.state.SchoolInput=this.value
_this.$router.push({
path: '/SearchSchool'
})
}
});
},
onCancel() {


},
onBlur() {
this.$store.state.SchoolInput=this.value
this.$router.push({
path: '/SearchSchool'
})

},
position(){
if(this.positionFlag){
$(".postion").animate({"height":"4.32rem"})
$(".position_img").animate({"transform":"rotate(180deg)"})
this.positionFlag=false
}else{
$(".postion").animate({"height":"2.16rem"})
$(".position_img").animate({'transform':"rotate(0deg)"})
this.positionFlag=true
}

},

province(){
if(this.$store.state.address==undefined){
$('.weui-dialog').css("height","3rem")
this.Alert=true
}else{
this.$store.state.SchoolSearch=this.$store.state.address;
this.$store.state.SchoolSearchType="province";
this.$router.push({
path: '/Allschool'
})

}
}
}

}



