


import ViewBox from 'vux/src/components/view-box/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default{
directives: {
TransferDom
},
name:"Serve",
components:{

Confirm,
ViewBox,
Tabbar,TabbarItem
},
data(){
return {
vuegConfig:{
forwardAnim: 'bounceInUp', //options所有配置可以写在这个对象里，会覆盖全局的配置
disable: false, //对于嵌套路由，默认为关闭动画，需要在组件的data.vuegConfig中配置disable为false启用
backAnim: 'bounceInDown'
},
vol:false,
newImg:false,
show:false
}
},
mounted(){
var _this=this
if(this.$store.state.ServeType=="志愿填报"){
this.vol=true
}else{
this.newImg=true
}

},
methods:{

download(){
$(".weui-dialog").css({"height":"3.2rem","width":"80%"})
this.show=true

},
onConfirm(){
window.location.href="https://www.xueguoguo.cn/jump.html"
}
}
}



