
import ViewBox from 'vux/src/components/view-box/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

const list = () => ['职业概述','热门招聘单位']
export default{
name:"JobDetail",
components:{
ViewBox,
Swiper,SwiperItem,Tab,TabItem
},
methods:{
change(index){
if(index==0){
this.$nextTick(function() {
var height=$("#fullBox").height()*2/100+"rem"

this.$refs.swiper.xheight = height
})
}



},
brStr(str){

if(str.indexOf("\n")!=-1){
var arr=str.split("\n");
var Newstr=""
for(var i=0;i<arr.length;i++){
Newstr+="<br/>"+arr[i]
}
return Newstr
}else{
return str
}
}

},
mounted(){
var _this=this
this.$http.get("https://www.xueguoguo.cn/api/v1/job/detail?id="+this.$store.state.JobId,{}).then(function(result){

_this.brief=_this.brStr(result.data.data.brief)

_this.future=_this.brStr(result.data.data.future)
_this.hotcompany=_this.brStr(result.data.data.hotcompany)
_this.$nextTick(function() {
var height=$("#fullBox").height()*2/100+"rem"

_this.$refs.swiper.xheight = height
})
_this.company=_this.hotcompany.split(",")
})
},
data(){
return {
vuegConfig:{
forwardAnim:'slideInLeft',
disable:false,
backAnim:'slideInRight',


},
index:0,
list2: list(),
demo2:'职业概述',
brief:"",
future:"",
hotcompany:"",
company:[]
}
}
}



