
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
name: "Allschool",
components: {
XHeader,
LoadMore,
Scroller,
Loading,
Spinner,
Divider,
Radio,
Group,
ViewBox
},
data() {
return {
vuegConfig:{
forwardAnim:'slideInRight',  //options所有配置可以写在这个对象里，会覆盖全局的配置
disable:false,//对于嵌套路由，默认为关闭动画，需要在组件的data.vuegConfig中配置disable为false启用
backAnim:'slideInLeft'
},
SchoolArr: [],
TypeArr: [],
index: 0,

pullupConfig2: {
content: '上拉加载更多',
downContent: '松开进行加载',
upContent: '上拉加载更多',
loadingContent: '加载中...'
},
pulldownConfig2: {
content: '下拉刷新',
downContent: '下拉刷新',
upContent: '释放刷新',
loadingContent: '加载中...'
},

noData: false,
noDataFlag: false,
status1: {
pulldownStatus: 'default'
},
positionFlag: true,
typeFlag: true,
filterFlag: true,
position: [],
positionData: "不限",
TypeData:"不限",
LevelData:"不限",
SchoolLevelData:"不限",
SexData:"不限",
province: "",
is985: "",
is211: "",
type: "",
sexRate: "",
level: "",
positionShow: false,
typeShow: false,
filterShow: false,
sexShow:false,
LevelArr: ["不限", "本科", "专科"],
schoolLevel: ["不限", "985", "211", "普通院校"],
sex: ["不限", "男生多", "女生多", "男女均衡"],
schoolLevelShow: false,
levelShow:false



}
},
mounted() {

this.firstIn()
var width = document.documentElement.clientWidth * 2 / 100 + "rem";

var height = document.documentElement.clientHeight * 2 / 100 + "rem";
$('.select_mask').css({
"width": width,
"height": height
});

this.getData();

this.$nextTick(() => {
this.$refs.demo2.reset()

})

},
methods: {
back() {
this.$router.go(-1);

},

search() {
this.$router.push({
path: '/SearchSchool'
})
},
showFilter() {

this.filterShow = true



this.index = 0;


if(this.filterFlag) {
this.levelShow=true
$('.select_mask').show()

$('.select_box li:eq(2)').css({
"z-index": "4"
})
$('.select_box li:eq(2)').children("i").animate({
"transform": "rotate(180deg)"
})
$('.select_each').animate({
"height": "4.92rem"
}, 300)
this.filterFlag = false
} else {
this.levelShow=false;
this.sexShow=false;
this.schoolLevelShow=false;
$('.select_each').animate({
"height": "0rem"
}, 300, function() {
$('.select_box li:eq(2)').css({
"z-index": "1"
})
$('.select_mask').hide()
$(".select_mask .select_each .filterBox .left li").eq(0).addClass("active").siblings("li").removeClass("active");
})

$('.select_box li:eq(2)').children("i").animate({
"transform": "rotate(0deg)"
})

this.filterFlag = true
this.filterShow = false
}

},
showMoreType() {
this.index = 0;
this.typeShow = true



if(this.typeFlag) {
$('.select_mask').show()
$('.select_box li:eq(1)').css({
"z-index": "4"
})
$('.select_box li:eq(1)').children("i").animate({
"transform": "rotate(180deg)"
})
$('.select_each').animate({
"height": "4.92rem"
}, 300)
this.typeFlag = false
} else {
$('.select_each').animate({
"height": "0rem"
}, 300, function() {
$('.select_box li:eq(1)').css({
"z-index": "1"
})
$('.select_mask').hide()
})

$('.select_box li:eq(1)').children("i").animate({
"transform": "rotate(0deg)"
})
this.typeShow = false
this.typeFlag = true
}

},
place() {
this.index = 0;


this.positionShow = true


if(this.positionFlag) {
$('.select_mask').show()
$('.select_box li:eq(0)').css({
"z-index": "4"
})
$('.select_box li:eq(0)').children("i").animate({
"transform": "rotate(180deg)"
})
$('.select_each').animate({
"height": "4.92rem"
}, 300)
this.positionFlag = false
} else {
$('.select_each').animate({
"height": "0rem"
}, 300, function() {
$('.select_box li:eq(0)').css({
"z-index": "1"
})
$('.select_mask').hide()
})

$('.select_box li:eq(0)').children("i").animate({
"transform": "rotate(0deg)"
})
this.positionShow = false
this.positionFlag = true
}

},
load1() {

setTimeout(() => {

this.status1.pulldownStatus = 'default'
}, 2000)
},

load2() {
setTimeout(() => {
if(this.noDataFlag == false) {
this.index += 1
this.getData()
setTimeout(() => {
this.$refs.demo2.donePullup()
}, 100)
}
if(this.noDataFlag) { // unload plugin
setTimeout(() => {
this.noData = true
this.$refs.demo2.disablePullup()

}, 200)
}
}, 2000)
},
getData() {
var _this = this;
var str = this.decide();

this.$http.get("https://www.xueguoguo.cn/api/v1/colleges/query?index=" + this.index + "&size=10" + str, {})
.then(function(result) {

if(result.data.msg == "没有数据") {
_this.noDataFlag = true;
_this.noData = true
_this.$refs.demo2.disablePullup()
return;
} else {
for(var i = 0; i < result.data.data.length; i++) {
if(result.data.data[i].luqu == "-") {
result.data.data[i].luqu = "暂无数据"
}
_this.SchoolArr.push(result.data.data[i])
}
}

})
},
decide() {
var str = "";

if(this.is211 != "") {
str += "&is211=" + this.is211
}
if(this.is985 != "") {
str += "&is985=" + this.is985
}
if(this.level != "") {
str += "&level=" + this.level
}
if(this.type != "") {
str += "&type=" + this.type
}
if(this.province != "") {
str += "&province=" + this.province
}
if(this.sexRate != "") {
str += "&sexRate=" + this.sexRate
}
return str;
},
changePosition(val) {
if(val == "不限") {
this.province = ""
} else {
this.province = val
}
this.SchoolArr.length = 0
this.getData()
$('.select_each').animate({
"height": "0rem"
}, 300, function() {
$('.select_box li:eq(0)').css({
"z-index": "1"
})
$('.select_mask').hide()
})

$('.select_box li:eq(0)').children("i").animate({
"transform": "rotate(0deg)"
})
this.positionShow = false
this.positionFlag = true

},
firstIn() {
if(this.$store.state.SchoolSearchType == "province") {
this.province = this.$store.state.SchoolSearch

} else if(this.$store.state.SchoolSearchType == "type") {
this.type = this.$store.state.SchoolSearch
} else if(this.$store.state.SchoolSearchType == "level") {
this.level = this.$store.state.SchoolSearch
} else if(this.$store.state.SchoolSearchType == "985") {
this.is985 = "1"
} else if(this.$store.state.SchoolSearchType == "211") {
this.is211 = "1"
}

for(var i = 0; i < this.$store.state.positionArr.length; i++) {
this.position.push(this.$store.state.positionArr[i].arr)
}
this.position.unshift("不限")
for(var i = 0; i < this.$store.state.typeArr.length; i++) {
this.TypeArr.push(this.$store.state.typeArr[i].arr)
}
this.TypeArr.unshift("不限")

},
changeType(val) {

if(val == "不限") {
this.type = ""
} else {
this.type = val
}
this.SchoolArr.length = 0
this.getData()
$('.select_each').animate({
"height": "0rem"
}, 300, function() {
$('.select_box li:eq(1)').css({
"z-index": "1"
})
$('.select_mask').hide()
})

$('.select_box li:eq(1)').children("i").animate({
"transform": "rotate(0deg)"
})
this.typeShow = false
this.typeFlag = true

},
changeLevel(val, label) {
if(val == "不限") {
this.level = ""
} else {
this.level = val
}
this.SchoolArr.length = 0
this.getData()


},
changeSchoolLevel(val){
if(val == "不限") {
this.is211=""
this.is985=""
} else if(val=="985"){
this.is985="1"
}else if(val=="211"){
this.is211="1"
}else{
this.is985="0"
this.is211="0"
}
this.SchoolArr.length = 0
this.getData()


},
changeSex(val){
if(val == "不限") {
this.sexRate = ""
} else {
this.sexRate = val
}
this.SchoolArr.length = 0
this.getData()
},
changeEach(index) {
$(".select_mask .select_each .filterBox .left li").eq(index).addClass("active").siblings("li").removeClass("active");
if(index==0){
this.levelShow=true;
this.sexShow=false;
this.schoolLevelShow=false;
}else if(index==1){
this.levelShow=false;
this.sexShow=false;
this.schoolLevelShow=true;
}else{

this.levelShow=false;
this.sexShow=true;
this.schoolLevelShow=false;
}



},
goSchool(id){
this.$store.state.id =id
this.$router.push({
path: '/School'
})
}
},
activated() {
this.$refs.demo2.reset()

}
}



