
import Zepto from '../../static/zepto.min.js'
import Progress from '../../static/progress.js'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
name: "VolSelect",
components: {
ViewBox,
Search,
Scroller,
Loading,
Spinner,
Radio,
Group
},
data() {
return {
vuegConfig: {
forwardAnim: 'slideInRight', //options所有配置可以写在这个对象里，会覆盖全局的配置
disable: false, //对于嵌套路由，默认为关闭动画，需要在组件的data.vuegConfig中配置disable为false启用
backAnim: 'slideInLeft'
},
results: [],
value: "",
SchoolArr: [],
TypeArr: [],
categoryArr: ["不限", "小概率", "可冲击", "较稳妥", "可保底"],

status1: {
pulldownStatus: 'default'
},
positionFlag: true,
typeFlag: true,
filterFlag: true,
positionArr: [],
positionData: "不限",
TypeData: "不限",
SearchText: "",

province: "",

type: "",
category: "",
positionShow: false,
typeShow: false,
filterShow: false,

categoryData: "不限",
AllSchool: true,
dataFlag: false,
noData: false,
showLoad:false
}
},
mounted() {
$(".weui-toast").css("min-height","2.5rem")
$('.vux-search-box').css({
"width": "5.38rem",
"left": "1rem"
});



var width = document.documentElement.clientWidth * 2 / 100 + "rem";

var height = document.documentElement.clientHeight * 2 / 100 + "rem";
$('.select_mask').css({
"width": width,
"height": height
});

this.firstIn()
this.getData();


},
methods: {
firstIn() {
for(var i = 0; i < this.$store.state.positionArr.length; i++) {
this.positionArr.push(this.$store.state.positionArr[i].arr)
}
this.positionArr.unshift("不限")
for(var i = 0; i < this.$store.state.typeArr.length; i++) {
this.TypeArr.push(this.$store.state.typeArr[i].arr)
}
this.TypeArr.unshift("不限")

},
draw() {

$(".myChart").each(function() {
$(this).ringChart();
})
},
back() {

this.$router.go(-1);

},
searchSchool() {

this.getData()

},

getResult(val) {
this.SearchText = val

},
onBlur(val) {

},
showFilter() {

this.filterShow = true

if(this.filterFlag) {

$('.select_mask').show()

$('.select_box li:eq(2)').css({
"z-index": "4"
})
$('.select_box li:eq(2)').children("i").animate({
"transform": "rotate(180deg)"
})
$('.select_each').animate({
"height": "4.92rem"
}, 300)
this.filterFlag = false
} else {

$('.select_each').animate({
"height": "0rem"
}, 300, function() {
$('.select_box li:eq(2)').css({
"z-index": "1"
})
$('.select_mask').hide()
$(".select_mask .select_each .filterBox .left li").eq(0).addClass("active").siblings("li").removeClass("active");
})

$('.select_box li:eq(2)').children("i").animate({
"transform": "rotate(0deg)"
})

this.filterFlag = true
this.filterShow = false
}

},
showMoreType() {
this.index = 0;
this.typeShow = true

if(this.typeFlag) {
$('.select_mask').show()
$('.select_box li:eq(1)').css({
"z-index": "4"
})
$('.select_box li:eq(1)').children("i").animate({
"transform": "rotate(180deg)"
})
$('.select_each').animate({
"height": "4.92rem"
}, 300)
this.typeFlag = false
} else {
$('.select_each').animate({
"height": "0rem"
}, 300, function() {
$('.select_box li:eq(1)').css({
"z-index": "1"
})
$('.select_mask').hide()
})

$('.select_box li:eq(1)').children("i").animate({
"transform": "rotate(0deg)"
})
this.typeShow = false
this.typeFlag = true
}

},
place() {
this.index = 0;

this.positionShow = true

if(this.positionFlag) {
$('.select_mask').show()
$('.select_box li:eq(0)').css({
"z-index": "4"
})
$('.select_box li:eq(0)').children("i").animate({
"transform": "rotate(180deg)"
})
$('.select_each').animate({
"height": "4.92rem"
}, 300)
this.positionFlag = false
} else {
$('.select_each').animate({
"height": "0rem"
}, 300, function() {
$('.select_box li:eq(0)').css({
"z-index": "1"
})
$('.select_mask').hide()
})

$('.select_box li:eq(0)').children("i").animate({
"transform": "rotate(0deg)"
})
this.positionShow = false
this.positionFlag = true
}

},

getData() {
this.showLoad=true
var _this = this;
this.dataFlag = false
this.AllSchool = true
this.noData = false
this.SchoolArr = []
this.$http.get("https://www.xueguoguo.cn/api/v1/colleges/recommand?token="+this.$store.state.token+"&batchName=" + this.$store.state.VolType, {})
.then(function(result) {
result.data.data.length=100
setTimeout(function(){
for(var i = 0; i < result.data.data.length; i++) {
if(result.data.data[i].luqu == "-") {
result.data.data[i].luqu = "暂无数据"
}

if(_this.SearchText != "" || _this.province != "" || _this.type != "" || _this.category != "") {
if(_this.SearchText != "") {
if(_this.province != "") {
if(_this.category != "") {
if(_this.type != "") {
if(result.data.data[i].name.indexOf(_this.SearchText) != -1 && _this.province == result.data.data[i].province && _this.category == result.data.data[i].category && _this.type == result.data.data[i].type) {
_this.SchoolArr.push(result.data.data[i])
_this.dataFlag = true;
}

} else if(result.data.data[i].name.indexOf(_this.SearchText) != -1 && _this.province == result.data.data[i].province && _this.category == result.data.data[i].category) {
_this.SchoolArr.push(result.data.data[i])
_this.dataFlag = true;
}

} else if(result.data.data[i].name.indexOf(_this.SearchText) != -1 && _this.province == result.data.data[i].province) {
_this.SchoolArr.push(result.data.data[i])
_this.dataFlag = true;
}

} else if(_this.type != "") {
if(result.data.data[i].name.indexOf(_this.SearchText) != -1 && _this.type == result.data.data[i].type) {
_this.SchoolArr.push(result.data.data[i])
_this.dataFlag = true;
}

} else if(result.data.data[i].name.indexOf(_this.SearchText) != -1) {
_this.SchoolArr.push(result.data.data[i])
_this.dataFlag = true;
}

} else if(_this.province != "") {
if(_this.category != "") {
if(_this.type != "") {
if(_this.province == result.data.data[i].province && _this.category == result.data.data[i].category && _this.type == result.data.data[i].type) {

_this.SchoolArr.push(result.data.data[i])
_this.dataFlag = true;
}
} else if(_this.province == result.data.data[i].province && _this.category == result.data.data[i].category) {
_this.SchoolArr.push(result.data.data[i])
_this.dataFlag = true;
}
} else if(_this.province == result.data.data[i].province) {
_this.SchoolArr.push(result.data.data[i])
_this.dataFlag = true;
}
} else if(_this.category != "") {
if(_this.type != "") {
if(_this.category == result.data.data[i].category && _this.type == result.data.data[i].type) {

_this.SchoolArr.push(result.data.data[i])
_this.dataFlag = true;
}
} else if(_this.category == result.data.data[i].category) {
_this.SchoolArr.push(result.data.data[i])
_this.dataFlag = true;
}

} else {
if(_this.type == result.data.data[i].type) {
_this.SchoolArr.push(result.data.data[i])
_this.dataFlag = true;
}
}

} else {
_this.dataFlag = true
_this.SchoolArr.push(result.data.data[i])
}

}

if(_this.$store.state.VolType == "本科批次") {
for(var j = 0; j < _this.SchoolArr.length; j++) {
for(var k = 0; k < _this.$store.state.VolData.data.batchs[0].colleges.length; k++) {

if(_this.SchoolArr[j].name == _this.$store.state.VolData.data.batchs[0].colleges[k].item.name) {
_this.SchoolArr.splice(j, 1)

}
}

}

}else{
for(var j = 0; j < _this.SchoolArr.length; j++) {
for(var k = 0; k < _this.$store.state.VolData.data.batchs[1].colleges.length; k++) {

if(_this.SchoolArr[j].name == _this.$store.state.VolData.data.batchs[1].colleges[k].item.name) {
_this.SchoolArr.splice(j, 1)

}
}

}
}

if(_this.dataFlag == false) {

_this.AllSchool = false
_this.noData = true
} else {
_this.SchoolArr = _this.unique1(_this.SchoolArr)

_this.$nextTick(() => {
_this.draw()
_this.$refs.demo2.reset()

})
}



_this.showLoad=false
},500)



})

},
changePosition(val) {
if(val == "不限") {
this.province = ""
} else {
this.province = val
}
this.SchoolArr.length = 0
this.getData()
$('.select_each').animate({
"height": "0rem"
}, 300, function() {
$('.select_box li:eq(0)').css({
"z-index": "1"
})
$('.select_mask').hide()
})

$('.select_box li:eq(0)').children("i").animate({
"transform": "rotate(0deg)"
})
this.positionShow = false
this.positionFlag = true

},

changeType(val) {

if(val == "不限") {
this.type = ""
} else {
this.type = val
}

this.getData()
$('.select_each').animate({
"height": "0rem"
}, 300, function() {
$('.select_box li:eq(1)').css({
"z-index": "1"
})
$('.select_mask').hide()
})

$('.select_box li:eq(1)').children("i").animate({
"transform": "rotate(0deg)"
})
this.typeShow = false
this.typeFlag = true

},
changeCategory(val) {

if(val == "不限") {
this.category = ""
} else {
this.category = val
}


this.getData()
$('.select_each').animate({
"height": "0rem"
}, 300, function() {
$('.select_box li:eq(2)').css({
"z-index": "1"
})
$('.select_mask').hide()
$(".select_mask .select_each .filterBox .left li").eq(0).addClass("active").siblings("li").removeClass("active");
})

$('.select_box li:eq(2)').children("i").animate({
"transform": "rotate(0deg)"
})

this.filterFlag = true
this.filterShow = false
},
goSchool(obj) {

if(this.$store.state.VolType == "本科批次") {
var newObj = {}
newObj.item = obj
newObj.group = ""
newObj.majors = "",
newObj.adjust = true
this.$store.state.VolData.data.batchs[0].colleges.push(newObj)

} else {
var newObj = {}
newObj.item = obj
newObj.group = ""
newObj.majors = "",
newObj.adjust = true
this.$store.state.VolData.data.batchs[1].colleges.push(newObj)
}
this.$router.go(-1);




},
unique1(arr) {
var res = [arr[0]];
for(var i = 1; i < arr.length; i++) {
var repeat = false;
for(var j = 0; j < res.length; j++) {
if(arr[i] == res[j]) {
repeat = true;
break;
}
}
if(!repeat) {
res.push(arr[i]);
}
}
return res;
}

},
activated() {
this.$refs.demo2.reset()

}
}



